<template>
  <div
    class="create-node"
    data-test="create-node"
  >
    <uc-modal
      :title="title"
      @hide="requestHideInput"
    >
      <div
        class="definition-picker"
      >
        <uc-select
          :placeholder="definitionPlaceholder"
          :options="availableDefinitions"
          :selected="selectedDefinitionGuid"
          @selected="selectedDefinitionGuid = $event.guid"
        >
          <template #pending>
            <loading-indicator :is-loading="true" />
          </template>
        </uc-select>
      </div>
      <uc-text-input
        ref="input"
        :label="nameLabel"
        :value="newName"
        @input="updateName"
      />
      <div
        v-show="errorMessage.length"
        class="create-product__error"
        data-test="create-product-error"
      >
        <span>{{ errorMessage }}</span>
      </div>
      <template #footer>
        <uc-button
          type="submit"
          :disabled="!enabled"
          @click="createNewNode"
        >
          {{ createLabel }}
        </uc-button>
        <uc-button
          theme="text"
          @click="requestHideInput"
        >
          {{ $tc('global.cancel') }}
        </uc-button>
      </template>
    </uc-modal>
  </div>
</template>

<script>
import UcModal from '../../../shared/components/widgets/uc-modal'
import UcSelect from '../../../shared/components/forms/uc-select'
import UcTextInput from '../../../shared/components/forms/uc-text-input'
import UcButton from '../../../shared/components/forms/uc-button'
import LoadingIndicator from '../../../shared/components/widgets/loading-indicator'
import {
  nodeTypeApiMap,
  nodeTypeSingular as nodeTypeSingularOf,
  catalog } from '../../js/tree-node-types'
import capitalize from '../../../shared/js/capitalize'
import { getEntityService } from '@/services'
import { getGuidAndLabelFromDefinitions } from '@/helpers/definition.helpers'

import { createNamespacedHelpers } from 'vuex'
const persistedStore = createNamespacedHelpers('persisted')

export default {
  name: 'CreateNode',
  components: {
    LoadingIndicator,
    UcModal,
    UcSelect,
    UcTextInput,
    UcButton
  },
  props: {
    nodeType: {
      type: String,
      default: ''
    },
    parentInfo: {
      type: Object,
      default: () => ({})
    },
    inputVisible: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      newName: '',
      errorMessage: '',
      availableDefinitions: undefined,
      selectedDefinitionGuid: ''
    }
  },
  computed: {
    ...persistedStore.mapGetters(['selectedStoreGuid']),
    nodeTypeSingular () {
      return nodeTypeSingularOf[this.nodeType]
    },
    title () {
      return this.$t('createNode.title', { nodeType: this.nodeTypeSingular })
    },
    translatedDefinition () {
      return this.$t('global.definition')
    },
    definitionLabel () {
      return capitalize(this.translatedDefinition)
    },
    definitionPlaceholder () {
      return `${capitalize(this.$t('global.select'))} ${this.translatedDefinition}`
    },
    nameLabel () {
      return `${capitalize(this.nodeTypeSingular)} ${this.$t('global.name')}`
    },
    createLabel () {
      return this.$t('createNode.addActionText', { nodeType: this.nodeTypeSingular })
    },
    isCatalog () {
      return this.nodeType === catalog
    },
    enabled () {
      if (this.isCatalog) {
        return this.newName.trim().length > 0
      } else {
        return this.newName.trim().length > 0 && this.selectedDefinitionGuid.length > 0
      }
    }
  },
  created () {
    this.entityService = getEntityService(this.nodeType)
  },
  mounted () {
    this.focusInput()
    this.initDefinitions()
    window.addEventListener('keyup', this.keyHandler)
  },
  destroyed () {
    window.removeEventListener('keyup', this.keyHandler)
  },
  methods: {
    requestShowInput () {
      this.$emit('requestShowInput')
    },
    requestHideInput () {
      this.$emit('requestHideInput')
    },
    focusInput () {
      this.$refs.input.focus()
    },
    updateName (name) {
      this.newName = name
      this.errorMessage = ''
    },

    async initDefinitions () {
      this.availableDefinitions = await this.loadDefinitions()
      if (this.availableDefinitions.length === 1) {
        this.selectedDefinitionGuid = this.availableDefinitions[0].guid
      }
    },
    async loadDefinitions () {
      const definitions = await this.entityService.fetchDefinitionsListAsync()
      const result = getGuidAndLabelFromDefinitions(definitions.data)
      return result
    },

    async createNewNode () {
      const payload = {
        ...this.parentInfo,
        sortOrder: 0,
        name: this.newName,
        definitionGuid: this.selectedDefinitionGuid
      }
      if (this.isCatalog) {
        payload.storeGuid = this.selectedStoreGuid
      }

      try {
        await this.axios.post(`${nodeTypeApiMap[this.nodeType]}`, payload)
        this.$emit('nodeCreated', { isCatalog: this.isCatalog, parentGuid: this.parentInfo.parentGuid })
        this.newName = ''
      } catch (err) {
        if (err.response.status === 400) {
          this.errorMessage = err.response.data.Message
        }
        this.focusInput()
      }
    },

    keyHandler (e) {
      if (e.key === 'Enter' && this.enabled) {
        this.createNewNode()
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.create-product__error
  box(100%, 50px)
  padding 6px 16px
  error-text()

.definition-picker
  margin-bottom 24px
</style>
