<template>
  <div class="warning-dialog">
    <uc-modal
      persistent
      :title="title"
      @hide="$emit('cancel')"
    >
      <p class="warning-dialog__content">
        {{ text }}
      </p>
      <template #footer>
        <uc-button
          ref="proceed"
          @click="$emit('proceed')"
        >
          {{ proceedButtonText }}
        </uc-button>
        <uc-button
          ref="cancel"
          theme="text"
          @click="$emit('cancel')"
        >
          {{ cancelButtonText }}
        </uc-button>
      </template>
    </uc-modal>
  </div>
</template>

<script>
import UcModal from '../uc-modal'
import UcButton from '../../forms/uc-button/uc-button'
export default {
  name: 'UcWarningDialog',
  components: {
    UcButton,
    UcModal
  },
  props: {
    text: {
      type: String,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    proceedButtonText: {
      type: String,
      default: function () { return this.$tc('global.proceed') }
    },
    cancelButtonText: {
      type: String,
      default: function () { return this.$tc('global.cancel') }
    }
  }
}
</script>

<style lang="stylus" scoped>
.warning-dialog__content
  secondary-details-text()
  margin 0
  text-align center
</style>
