import { mergeAllData, makeDefinitionsByDefinitionGuid } from '@/catalogs/js/merge-definitions-and-data'
import { nodeTypeApiMap } from '@/catalogs/js/tree-node-types'
export { EntityType } from '@/shared/js/constants'

export class EntityService {
  _definitionsLastModified = {}
  _definitionsResponseCache = {}

  constructor (entityType, httpClient) {
    this._httpClient = httpClient
    this._entityType = entityType
  }

  get _rootUrl () {
    return nodeTypeApiMap[this._entityType]
  }

  fetchDefinitionsAsync ({ cancelToken } = {}) {
    const url = `${this._rootUrl}/definitions/full`
    return this._fetchDefinitionsAsync({ url, cancelToken })
  }

  fetchDefinitionsListAsync ({ cancelToken } = {}) {
    const url = `${this._rootUrl}/definitions`
    return this._fetchDefinitionsAsync({ url, cancelToken })
  }

  async fetchEntitiesAsync ({ cultureCode, params }) {
    const response = await this._httpClient.get(`${this._rootUrl}/${cultureCode}`, { params })

    return {
      ...response,
      data: await this._decorateEntityWithDefinitionsAsync(response.data)
    }
  }

  async fetchEntityAsync ({ guid, cultureCode }) {
    const response = await this._httpClient.get(`${this._rootUrl}/${guid}/${cultureCode}`)

    return {
      ...response,
      data: (await this._decorateEntityWithDefinitionsAsync([response.data]))[0]
    }
  }

  fetchEntityListAsync () {
    return this._httpClient.get(`${this._rootUrl}/shallow`)
  }

  createEntityAsync ({ name, definitionGuid, params }) {
    return this._httpClient.post(this._rootUrl, { name, definitionGuid, ...params })
  }

  async _fetchDefinitionsAsync ({ url, cancelToken }) {
    const headers = {}

    if (this._definitionsLastModified[url]) {
      headers['If-Modified-Since'] = this._definitionsLastModified[url]
    }

    try {
      const response = await this._httpClient.get(url, { headers, cancelToken })

      this._definitionsLastModified[url] = response.headers['last-modified']
      this._definitionsResponseCache[url] = response
      return this._definitionsResponseCache[url]
    } catch (e) {
      switch (e.response.status) {
        case 304:
          return this._definitionsResponseCache[url]
        default:
          throw new Error(e)
      }
    }
  }

  async _decorateEntityWithDefinitionsAsync (entities) {
    const response = await this.fetchDefinitionsAsync()
    const definitions = makeDefinitionsByDefinitionGuid(response.data)
    return mergeAllData(definitions, entities)
  }
}
