<template>
  <div
    class="uc-date-time-picker"
  >
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="formattedDateTime"
      :required="isRequired"
    >
      <div class="uc-date-time-picker__row">
        <div class="uc-date-time-picker__column">
          <uc-popup-calendar
            v-bind="{ label, value, hasErrors, language }"
            :readonly="isReadonly"
            @selected="onSelected"
          />
        </div>
        <div class="uc-date-time-picker__column">
          <input-group
            v-bind="{ label, hasErrors }"
            :readonly="isReadonly"
          >
            <time-picker
              v-model="time"
              class="time-picker"
              :disabled="isReadonly"
              hide-clear-button
            />
          </input-group>
        </div>
      </div>
    </editor-wrapper>
  </div>
</template>

<script>
import UcPopupCalendar from '../forms/uc-popup-calendar'
import TimePicker from 'vue2-timepicker'
import { Format as format, locales } from '@/shared/js/date-formatter'
import editorMixin from './editor-mixin'
import InputGroup from '../forms/input-group'

const dateFormats = {
  'default': 'MMMM D YYYY'
}

export default {
  name: 'UcDateTimePicker',
  components: {
    UcPopupCalendar,
    TimePicker,
    InputGroup
  },
  mixins: [ editorMixin ],
  computed: {
    date: {
      get: function () {
        return this.definition.value
      },
      set: function (date) {
        this.changeDate(date)
      }
    },
    time: {
      get: function () {
        const date = this.getDate()

        return {
          HH: date ? date.getHours().toString().padStart(2, '0') : null,
          mm: date ? date.getMinutes().toString().padStart(2, '0') : null
        }
      },
      set: function (time) {
        this.changeTime(time)
      }
    },
    formattedDateTime () {
      return this.value ? `${this.dateFormatter(this.value)}, ${this.timeFormatter()}` : ''
    },
    dateFormat () {
      return dateFormats[this.language] || dateFormats.default
    }
  },
  methods: {
    getDate () {
      if (!this.value) {
        return null
      }

      const date = new Date(this.value)
      if (date instanceof Date && !isNaN(date)) {
        // Truncate date to make comparison possible
        return new Date(
          date.getFullYear(),
          date.getMonth(),
          date.getDate(),
          date.getHours(),
          date.getMinutes()
        )
      } else {
        return null
      }
    },
    changeTime (time) {
      const date = this.getDate()
      if (date) {
        const dateTime = new Date(
          date.getFullYear(),
          date.getMonth(),
          date.getDate(),
          time.HH,
          time.mm
        )

        this.emitIfChanged(dateTime)
      }
    },
    onSelected (newDate) {
      const date = this.getDate() || new Date()
      if (date) {
        const dateTime = new Date(
          newDate.getFullYear(),
          newDate.getMonth(),
          newDate.getDate(),
          date.getHours(),
          date.getMinutes()
        )

        this.emitIfChanged(dateTime)
      }
    },
    emitIfChanged (dateTime) {
      const previousDate = this.getDate()
      if (!previousDate || dateTime.getTime() !== previousDate.getTime()) {
        this.onChange({
          newValue: dateTime,
          oldValue: previousDate
        })
      }
    },
    dateFormatter (date) {
      return this.date ? format(date, this.dateFormat, locales[this.language]) : ''
    },
    timeFormatter () {
      return `${this.time.HH}:${this.time.mm}`
    }
  }
}
</script>
<!-- eslint-disable vue-scoped-css/require-scoped -->
<style lang="stylus">
.uc-date-time-picker__row
  display flex

.uc-date-time-picker__column
  width 50%

  &:first-child
    margin-right 8px

  &:last-child
    margin-left 8px

.time-picker
  width 100%
  primary-body-text()
  vertical-align inherit

  .display-time
    height 100%
    background-color transparent
    outline none

  ul > li
    line-height 25px

  ul.hours > li:not(.hint):not(.disabled):not(.active)
  ul.minutes > li:not(.hint):not(.disabled):not(.active)
    border 1px solid transparent

  ul.hours > li:not(.hint):not(.disabled):not(.active):hover
  ul.minutes > li:not(.hint):not(.disabled):not(.active):hover
    border 1px solid $ucommerce-light-blue
    background-color transparent

  ul.hours > li:not(.hint):not(.disabled).active
  ul.minutes > li:not(.hint):not(.disabled).active
    color $primary-text
    background $ucommerce-light-blue

  input[type=text]
    width 100%
    height 48px
    padding 0 16px
    border none
    overflow hidden
    text-align left
    cursor pointer
    primary-body-text()

    &::placeholder
      secondary-body-text()

    &:disabled
      color #cacad7

  .dropdown
    top 48px
    width 100%
    border 1px solid $col_lightest_gray
    border-radius 4px
    box-shadow none

    .select-list
      width 100%

    .hint
      display none
</style>
