<template>
  <button
    class="uc-button-round"
    :class="cssClassObject"
    :disabled="disabled"
    data-test="uc-button-round"
    type="button"
    @click="onClick"
  >
    <slot />
  </button>
</template>

<script>
import { validateAgainstEnum } from '@/shared/js/validate-against'

export const themeProp = {
  PRIMARY: 'primary',
  SECONDARY: 'secondary',
  WHITE: 'white',
  GHOST: 'ghost'
}

export const sizeProp = {
  S: 's',
  M: 'm',
  L: 'l'
}

export default {
  name: 'UcButtonRound',
  props: {
    size: {
      type: String,
      default: sizeProp.M,
      validator: validateAgainstEnum(sizeProp)
    },
    theme: {
      type: String,
      default: themeProp.PRIMARY,
      validator: validateAgainstEnum(themeProp)
    },
    disabled: {
      type: Boolean,
      default: false
    },
    floating: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    cssClassObject () {
      return {
        'uc-button-round--size-s': this.size === sizeProp.S,
        'uc-button-round--size-m': this.size === sizeProp.M,
        'uc-button-round--size-l': this.size === sizeProp.L,
        'uc-button-round--theme-primary': this.theme === themeProp.PRIMARY,
        'uc-button-round--theme-secondary': this.theme === themeProp.SECONDARY,
        'uc-button-round--theme-ghost': this.theme === themeProp.GHOST,
        'uc-button-round--theme-white': this.theme === themeProp.WHITE,
        'is-disabled': this.disabled,
        'is-floating': this.floating
      }
    }
  },
  methods: {
    onClick (e) {
      this.$emit('click', e)
    }
  }
}
</script>

<style lang="stylus" scoped>
.uc-button-round
  position relative
  display inline-flex
  align-items center
  justify-content center
  padding 0
  border none
  border-radius 50%
  outline none
  cursor pointer

  &:hover
    box-shadow $base-box-shadow

  &--size-s
    box(24px)

  &--size-m
    box(32px)

  &--size-l
    box(48px)

  &--theme-primary
    color $ucommerce-white
    background-color $ucommerce-blue

  &--theme-secondary
    color $ucommerce-white
    background-color $primary-text

  &--theme-white
    color $primary-text
    background-color $ucommerce-white

  &--theme-ghost
    border solid 1px $input-field-stroke
    color $primary-text
    background-color transparent

    &.is-disabled
      color $builder-selected-node-bg
      background-color transparent

  &.is-floating
    box-shadow 0 1px 4px 0 rgba(0, 0, 0, 0.1)

    &:active
      box-shadow 0 2px 8px 0 rgba(0,0,0,0.2)
      transform scale(0.97)

  &.is-disabled
    color $ucommerce-white
    text-decoration none
    background-color $input-field-stroke
    cursor default

    &:hover
      box-shadow none
</style>
