<template>
  <label
    class="uc-radio"
    :class="{ readonly, 'uc-radio--with-icon': hasIcon }"
    data-test="uc-radio"
  >
    <span
      v-truncate
      class="uc-radio__label"
    >
      <slot>{{ label }}</slot>
    </span>
    <input
      :id="id"
      :checked="isChecked"
      type="radio"
      class="uc-radio__input"
      :name="group"
      :disabled="readonly"
      :value="value"
      @[event]="onChange"
    >
    <i
      class="uc-radio__icon"
      :class="{ 'uc-radio__icon--default': !hasIcon }"
    >
      <slot name="icon" />
    </i>
  </label>
</template>

<script>
import getId from '../../../js/id-generator.js'

export default {
  name: 'UcRadio',
  props: {
    label: {
      type: String,
      required: true
    },
    value: {
      type: String,
      required: true
    },
    readonly: {
      type: Boolean,
      default: false
    },
    checked: {
      type: Boolean,
      default: false
    },
    group: {
      type: String,
      default: ''
    },
    nullable: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isChecked: false
    }
  },
  computed: {
    id () {
      return getId()
    },
    event () {
      return this.nullable ? 'click' : 'change'
    },
    hasIcon () {
      return this.$slots.icon
    }
  },
  watch: {
    checked: {
      handler (value) {
        this.isChecked = value
      },
      immediate: true
    }
  },
  methods: {
    onChange () {
      this.isChecked = this.nullable ? !this.isChecked : true
      this.$emit('change', { label: this.label, value: this.value, checked: this.isChecked })
    }
  }
}
</script>

<style lang="stylus" scoped>
$space = 8px
$icon-size = 3 * $space

.uc-radio
  position relative
  display flex
  flex-direction row-reverse
  align-items center
  cursor pointer
  box(100%)
  primary-details-text()

  &.readonly
    opacity 0.6
    user-select none
    pointer-events none

  &.uc-radio--with-icon
    primary-body-text()
    flex-direction row

.uc-radio__label
  flex-grow 1
  flex-shrink 1
  margin-right $space

  .uc-radio--with-icon &
    flex-grow 0

.uc-radio__icon
  display flex
  flex-shrink 0

.uc-radio__icon--default
  align-items center
  justify-content center
  margin-right $space
  border 1px solid $col_pale_lilac
  border-radius 100%
  box(3 * $space)

.uc-radio__input
  position absolute
  display none

  &:checked
    & ~ .uc-radio__icon--default::after
      display inline-block
      border-radius 100%
      background-color $primary-text
      content ''
      box(10px)

  .uc-radio--with-icon &
    &:not(:checked)
      & ~ .uc-radio__icon
        visibility hidden
</style>
