<template>
  <div class="uc-number">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="value"
      :required="isRequired"
    >
      <uc-numeric-input
        v-bind="{ label, placeholder, hasErrors }"
        :readonly="isReadonly"
        :value="value !== null ? Number(value) : null"
        @blur="onBlur"
      />
    </editor-wrapper>
  </div>
</template>

<script>
import UcNumericInput from '../forms/uc-numeric-input'
import editorMixin from './editor-mixin'

export default {
  name: 'UcNumber',
  components: {
    UcNumericInput
  },
  mixins: [ editorMixin ],
  methods: {
    onBlur (e) {
      const newValue = e.target.value
      const oldValue = this.value
      const isSameValue = oldValue === newValue || ((oldValue === undefined || oldValue === null) && newValue === '')

      if (isSameValue && !this.hasErrors) {
        return
      }

      this.onChange({
        newValue,
        oldValue
      })
    }
  }
}
</script>
