<template>
  <div class="uc-image-picker-grid-file">
    <uc-image-picker-node
      :selected="selected"
      :node="node"
      @click="select"
    >
      <file-icon />
    </uc-image-picker-node>
  </div>
</template>

<script>
import FileIcon from '@/shared/assets/illustrations/file-image'
import UcImagePickerNode from '../uc-image-picker-node'

const ComponentEvent = {
  SELECT: 'select'
}

export default {
  name: 'UcImagePickerGridFile',
  components: {
    FileIcon,
    UcImagePickerNode
  },
  props: {
    node: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    select () {
      this.$emit(ComponentEvent.SELECT, this.node)
    }
  }
}
</script>
