<template>
  <uc-navigator
    v-bind="{ nodes, isEmptyRootState, navigation, isLoading }"
    :no-items-title="$t('ucContentPicker.noItemsTitle')"
    :no-items-text="$t('ucContentPicker.noItemsText', { name: hostSystemName })"
    @navigate="navigate"
  >
    <template #actions>
      <div class="content-navigator__actions">
        <slot name="actions" />
      </div>
    </template>

    <template #content>
      <!-- View -->
      <uc-navigator-view-grid-spacious :nodes="nodes">
        <template v-slot="{ item }">
          <content-navigator-grid-node
            :node="item"
            :selected="isNodeSelected(item.guid)"
            @select="onSelect"
            @open="onOpen"
          />
        </template>
      </uc-navigator-view-grid-spacious>
    </template>
  </uc-navigator>
</template>

<script>
import { UcNavigator, UcNavigatorViewGridSpacious } from '@/shared/components/generic/uc-navigator'
import ContentNavigatorGridNode from '../content-navigator-grid-node'

const ComponentEvent = {
  SELECT_NODE: 'selectNode',
  OPEN_NODE: 'openNode'
}

export default {
  name: 'ContentNavigator',
  components: {
    UcNavigator,
    UcNavigatorViewGridSpacious,
    ContentNavigatorGridNode
  },
  props: {
    nodes: {
      type: Array,
      required: true
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    isEmptyRootState: {
      type: Boolean,
      default: false
    },
    selectedNodesGuids: {
      type: Array,
      default: () => []
    },
    hostSystemName: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      navigation: [{
        name: this.$t('ucContentPicker.item'),
        guid: -1,
        item: null
      }]
    }
  },
  methods: {
    isNodeSelected (guid) {
      return this.selectedNodesGuids.includes(guid)
    },
    navigate (node) {
      node.guid === -1 ? this.navigateToRootNode() : this.navigateToNode(node)
    },
    navigateToRootNode () {
      this.openNode(null)
      this.navigation = [this.navigation[0]]
    },
    navigateToNode (node) {
      const indexOf = this.navigation.indexOf(node)
      this.openNode(node.item)
      this.navigation.splice(indexOf + 1, this.navigation.length)
    },
    openNode (node) {
      this.$emit(ComponentEvent.OPEN_NODE, node)
    },
    onSelect (node) {
      this.$emit(ComponentEvent.SELECT_NODE, node)
    },
    onOpen (node) {
      this.openNode(node)
      this.navigation.push({
        name: node.name,
        guid: node.id,
        item: node
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
.content-navigator__actions
  display flex
  flex-direction row
</style>
