<template>
  <div>
    <div class="properties_list">
      <div
        v-for="property in properties"
        :key="property.index"
        class="property"
        @click.prevent.stop="toggleColumn(property)"
      >
        <uc-checkbox
          :label="property.label"
          :checked="property.show"
        />
        <span class="property_definition">
          <info-icon
            v-if="property.definitions.length > 1"
            class="icon"
          >
            {{ $tc('propertiesList.informationText', { partOf : property.definitions.toString() }) }}
          </info-icon>
          <span v-else>{{ property.definitions[0] }}</span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>

import UcCheckbox from '../../../shared/components/forms/uc-checkbox'
import InfoIcon from '../../../shared/icons/legacy/info'
import { createNamespacedHelpers } from 'vuex'
const genericListStore = createNamespacedHelpers('genericList')

export default {
  name: 'PropertiesList',
  components: {
    UcCheckbox,
    InfoIcon

  },
  props: {
    title: {
      type: String,
      default: ''
    },
    properties: {
      type: Array,
      default: () => []
    },
    onClickAll: {
      type: Boolean,
      default: false
    },
    onClickSingle: {
      type: Boolean,
      default: false
    },
    listId: {
      type: String,
      required: true
    }
  },
  methods: {
    ...genericListStore.mapMutations(['toggleListColumn']),
    toggleColumn (property) {
      const show = (this.onClickSingle) ? !property.show : this.onClickAll
      this.toggleListColumn({ id: this.listId, index: property.index, show: show })
    },
    getCheckClassObject: function (property) {
      return {
        'added': property !== undefined ? property.show : false
      }
    }
  }
}
</script>

<style lang="stylus" scoped>

@-webkit-keyframes fadeInProperty {
  0%   { opacity: 0 }

  100% { opacity: 1 }
}

.properties_title
  padding 6px 24px
  background-color $col_grey1

.properties_list
  overflow hidden

.property
  position relative
  display flex
  padding 10px 30px
  cursor pointer
  animation ease alternate
  animation-name fadeInProperty
  animation-duration 250ms

  &:hover
    background-color $col_hover2

.property_definition
  margin-left auto
  color #b9babb

.icon
  circle(16px)
  display flex

  svg
    margin auto
    fill rgba(0,0,0,0.2)

  &:hover
    background-color $col_hover1

/deep/ label.uc-checkbox
  truncate()
</style>
