<template>
  <div
    class="product-search"
  >
    <!-- Search input -->
    <div class="product-search__input">
      <uc-search
        v-model.trim="searchTerm"
        :placeholder="searchPlaceholder"
      />
    </div>

    <!-- Search results -->
    <div class="product-search__results">
      <list
        v-show="searchTerm.length > 0"
        :id="listId"
        v-bind="{ dataUrl, defaultColumnsConfiguration, listType }"
        :header="$tc('list.products')"
        node-type="product"
        selectable-rows
        @sortBy="onSortBy"
      >
        <template #actionsFooter="{ selected, clearSelected }">
          <uc-button
            :disabled="selected.length === 0"
            floating
            @click="selectProducts(selected, clearSelected)"
          >
            {{ `${selectButtonText} (${ selected.length })` }}
          </uc-button>
        </template>
      </list>
    </div>
  </div>
</template>

<script>
import UcButton from '@/shared/components/forms/uc-button/uc-button'
import UcSearch from '@/shared/components/forms/uc-search'
import setUrlParam from '@/shared/js/set-url-param'
import List, { ListType } from './list'
import { validateAgainstEnum } from '@/shared/js/validate-against'

export { ListType }

export default {
  name: 'ProductSearch',
  components: {
    UcSearch,
    UcButton,
    List
  },
  props: {
    contentLanguage: {
      type: String,
      default: ''
    },
    selectButtonText: {
      type: String,
      required: true
    },
    searchPlaceholder: {
      type: String,
      default: function () {
        return this.$tc('global.search')
      }
    },
    showVariants: {
      type: Boolean,
      default: false
    },
    listType: {
      type: String,
      default: ListType.PRODUCT_LIST,
      validate: validateAgainstEnum(ListType)
    }
  },
  data () {
    return {
      searchTerm: '',
      sortBy: ''
    }
  },
  computed: {
    dataUrl () {
      let url = `/products/${this.contentLanguage}`

      url = setUrlParam(url, 'sort', this.sortBy)
      url = setUrlParam(url, 'searchTerm', this.searchTerm)

      if (this.showVariants) {
        url = setUrlParam(url, 'includeVariants', true)
      }

      return url
    },
    defaultColumnsConfiguration () {
      if (this.showVariants) {
        return {
          'primaryImage': { 'width': '90px' },
          'sku': { 'width': '150px' },
          'variantSku': { 'width': '150px' },
          'name': { 'width': '308px' }
        }
      }

      return {
        'primaryImage': { 'width': '90px' },
        'sku': { 'width': '100px' },
        'name': { 'width': '508px' }
      }
    },
    listId () {
      if (this.showVariants) {
        return 'ProductAndVariantSearch'
      } else {
        return 'ProductSearch'
      }
    }
  },
  methods: {
    selectProducts (products) {
      this.$emit('selection', products)
    },
    onSortBy (sortBy) {
      this.sortBy = sortBy
    }
  }
}
</script>
<style scoped lang="stylus">
.product-search
  display flex
  flex-direction column
  width 100%
  height 100%

.product-search__results
  flex 1 0 auto
  height 0

  >>> .list-table-container
    border 1px solid $platform-bg
    border-top none
</style>
