<template>
  <div class="uc-navigator">
    <!-- Loading state -->
    <div
      v-show="isLoading"
      class="uc-navigator__pending"
      data-test="loading-indicator"
    >
      <loading-indicator :is-loading="isLoading" />
    </div>
    <template v-show="!isLoading">
      <!-- Empty state -->
      <slot
        v-if="isEmptyRootState"
        name="emptyContent"
      >
        <uc-navigator-empty-state
          :title="noItemsTitle"
          :text="noItemsText"
        />
      </slot>

      <template v-if="!isEmptyRootState">
        <!-- Header -->
        <div
          v-if="navigation.length > 0"
          class="uc-navigator__header"
        >
          <div class="uc-navigator__header--inline">
            <uc-breadcrumbs
              :items="navigation"
              @click="navigate"
            />
            <slot name="actions" />
          </div>
        </div>
        <slot
          v-if="nodes.length === 0"
          name="emptyFolder"
        />

        <!-- Content -->
        <div
          v-else
          class="uc-navigator__content"
        >
          <uc-infinite-scroller
            data-test="infinite-scroller"
            v-bind="{ scrollTo }"
            :disabled="disableInfiniteScroll"
            @next="requestNextPage"
          >
            <template #default>
              <slot
                name="content"
                :nodes="nodes"
              />
            </template>
            <template #trigger>
              <slot name="content-footer" />
            </template>
          </uc-infinite-scroller>
        </div>
      </template>
    </template>
  </div>
</template>

<script>
import LoadingIndicator from '@/shared/components/widgets/loading-indicator'
import UcBreadcrumbs from '@/shared/components/widgets/uc-breadcrumbs/uc-breadcrumbs'
import UcInfiniteScroller from '@/shared/components/generic/uc-infinite-scroller'
import UcNavigatorEmptyState from './components/uc-navigator-empty-state'

const ComponentEvent = {
  NAVIGATE: 'navigate',
  REQUEST_NEXT_PAGE: 'requestNextPage'
}

export default {
  name: 'UcNavigator',
  components: {
    UcBreadcrumbs,
    LoadingIndicator,
    UcNavigatorEmptyState,
    UcInfiniteScroller
  },
  props: {
    nodes: {
      type: Array,
      required: true
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    navigation: {
      type: Array,
      default: () => []
    },
    isEmptyRootState: {
      type: Boolean,
      default: false
    },
    noItemsTitle: {
      type: String,
      default: ''
    },
    noItemsText: {
      type: String,
      default: ''
    },
    scrollTo: {
      type: Array,
      default: () => [],
      validator: (value) => value.length <= 2 && value.every(entry => Number.isInteger(entry))
    },
    disableInfiniteScroll: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    navigate (breadcrumb) {
      this.$emit(ComponentEvent.NAVIGATE, breadcrumb)
    },
    requestNextPage () {
      this.$emit(ComponentEvent.REQUEST_NEXT_PAGE)
    }
  }
}

</script>

<style scoped lang="stylus">
.uc-navigator
  position relative
  display flex
  flex 1
  flex-direction column
  height 100%

.uc-navigator__pending
  position absolute
  top 0
  right 0
  bottom 0
  left 0
  z-index 100
  display flex
  align-items center
  justify-content center
  background-color rgba(255, 255, 255, 1)

.uc-navigator__header
  height 48px

.uc-navigator__header--inline
  display flex
  align-items center
  justify-content space-between

.uc-navigator__content
  flex 1 0 auto
  height 0

</style>
