<template>
  <div class="settings-container">
    <uc-button-round
      theme="ghost"
      size="m"
      @click="toggleSettingsMenu"
    >
      <i class="icon icon--truncate">
        <truncate-icon />
      </i>
    </uc-button-round>
    <nav
      v-if="toggled"
      :class="{'show': toggled}"
      class="settings-menu"
    >
      <div class="arrow-up" />
      <div class="settings-menu_search">
        <uc-text-input
          :value="searchTerm"
          class="settings-menu_search_input"
          placeholder="Search"
          label="Search Properties"
          @input="onSearch"
        />
      </div>
      <div class="settings-menu_properties-lists">
        <div
          v-custom-scroll-bar
          class="settings__simplebar"
        >
          <div v-if="searchTerm === ''">
            <properties-list
              :properties="shownProperties"
              :on-click-all="false"
              :list-id="listId"
              title="Visible"
            />
            <properties-list
              :properties="hiddenProperties"
              :on-click-all="true"
              :list-id="listId"
              title="Not visible"
            />
          </div>
          <div v-if="searchTerm !== '' && foundProperties.length > 0">
            <properties-list
              :title="foundTitle"
              :properties="foundProperties"
              :on-click-single="true"
              :list-id="listId"
            />
          </div>
          <div
            v-if="searchTerm !== '' && foundProperties.length === 0"
            class="no-results"
          >
            {{ $t('global.searchNoResults') }}
          </div>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
import UcButtonRound from '../../../shared/components/forms/uc-button-round'
import { Truncate16Thick as TruncateIcon } from '../../../shared/icons'
import PropertiesList from './properties-list'
import UcTextInput from '../../../shared/components/forms/uc-text-input'
import { createNamespacedHelpers } from 'vuex'
const genericListStore = createNamespacedHelpers('genericList')

export default {
  name: 'Settings',
  components: {
    UcButtonRound,
    TruncateIcon,
    PropertiesList,
    UcTextInput
  },
  props: {
    listId: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      toggled: false,
      searchTerm: '',
      listData: null,
      settingsItem: null
    }
  },
  computed: {
    ...genericListStore.mapGetters(['list']),
    foundProperties () {
      return this.listData.columns.filter(x => x.label.toLowerCase().includes(this.searchTerm))
    },
    foundTitle () {
      return `Found ${this.foundProperties.length} matches`
    },
    shownProperties () {
      return this.listData.columns.filter(x => x.show)
    },
    hiddenProperties () {
      return this.listData.columns.filter(x => !x.show)
    }
  },
  mounted () {
    this.settingsItem = this.$el
  },
  created () {
    this.listData = this.list(this.listId)
  },
  methods: {
    ...genericListStore.mapActions(['applyDefaultColumnOrder']),
    toggleSettingsMenu () {
      if (!this.toggled) {
        window.addEventListener('click', this.closeSettingsFromOutside)
      }

      this.searchTerm = ''
      this.toggled = !this.toggled
    },
    selectDefault () {
      this.searchTerm = ''
      this.applyDefaultColumnOrder({ id: this.listId, currentList: this.listData })
    },
    onSearch (value) {
      this.searchTerm = value.toLowerCase()
    },

    closeSettingsFromOutside (e) {
      let element = this.settingsItem
      if (e.target !== element && !element.contains(e.target)) {
        this.toggleSettingsMenu()
        window.removeEventListener('click', this.closeSettingsFromOutside)
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
$property-list_min-height = 250px
$property-list_max-height = 245px

.outside-mask
  position relative
  top 0
  right 0
  bottom 0
  left 0
  width 100%
  height 100%
  background-color red

.arrow-up
  position relative
  top -10px
  width 0
  height 0
  margin 0 calc(100% - 26px)
  border-right 10px solid transparent
  border-bottom 10px solid #fff
  border-left 10px solid transparent

.settings__simplebar
  min-height $property-list_min-height
  max-height $property-list_max-height
  overflow-y auto

.settings-container
  position relative
  z-index 103
  display flex
  flex-direction column

.settings-trigger
  display flex
  align-items center
  margin-left auto
  cursor pointer

.settings-icon
  circle(26px)
  display flex
  transition background-color 200ms

  svg
    box(80%)
    margin auto
    fill rgba(0,0,0,0.2)

  &:hover
    background-color $col_hover1

.settings-label
  color $col_label

  &:hover
    text-decoration underline

.settings-menu
  position absolute
  top 50px
  right 0
  z-index 20
  width 240px
  background-color $col_bg
  box-shadow 0 4px 30px 0 rgba(0, 0, 0, 0.15)
  transform translate(-11px, -11px) scale(0)
  pointer-events none

  &.show
    transform translate(0, 0) scale(1)
    pointer-events all

.settings-menu_search
  width auto
  padding 10px 20px 20px 20px
  border-bottom solid 1px $input-field-stroke

.search-icon-container
  box(18px)
  position relative
  padding 9px
  opacity 0.5

.no-results
  width 100%
  height $property-list_min-height
  height 100%
  margin-top 20px
  color $col_grey0
  font-size 16px
  line-height 20px
  text-align center

.actions
  display flex
  justify-content space-between
  padding 12px
  border-top 1px solid $input-field-stroke

.icon--truncate svg
  position absolute
  top 50%
  left 50%
  transform: translate(-50%, -70%)
</style>
