<template>
  <uc-grid
    :items="nodes"
    :columns="2"
    :grid-gap="gridGap"
  >
    <template v-slot="{item}">
      <slot :item="item" />
    </template>
  </uc-grid>
</template>

<script>
import UcGrid, { GridGapProp } from '@/shared/components/generic/uc-grid/uc-grid'
import { validateAgainstEnum } from '@/shared/js/validate-against'

export default {
  name: 'UcNavigatorViewGridSpacious',
  components: {
    UcGrid
  },
  props: {
    nodes: {
      type: Array,
      required: true
    },
    gridGap: {
      type: String,
      default: GridGapProp.MD,
      validator: validateAgainstEnum(GridGapProp)
    }
  }
}
</script>
