<template>
  <div class="details__info">
    <div
      :class="{toggled: toggled}"
      class="details__info-wrapper"
      @click="toggleInfo"
    >
      <cog class="cog" />
    </div>
    <transition name="popover-right">
      <div
        v-show="toggled"
        class="dropdown-overlay details__info-dropdown"
      >
        <slot />
      </div>
    </transition>
  </div>
</template>

<script>
import cog from '../../../shared/icons/legacy/coq'
export default {
  name: 'Info',
  components: {
    cog
  },
  data () {
    return {
      toggled: false
    }
  },
  methods: {
    hide () {
      this.toggled = false
    },
    toggleInfo (event) {
      event.preventDefault()
      this.toggled = !this.toggled
      if (this.toggled) {
        this.$nextTick(() => {
          window.addEventListener('click', this.closeHandler)
          this.$el.addEventListener('click', function (e) { e.stopPropagation() })
        })
      }
    },
    closeHandler () {
      this.toggled = false
      window.removeEventListener('click', this.closeHandler)
    }
  }
}
</script>

<style lang="stylus" scoped>
.details__info
  position relative
  display inline-block
  font-size 0.8125rem
  white-space nowrap

.details__info-wrapper
  padding 4px 8px
  border-radius 4px
  transition background-color 200ms

  &.toggled
    background-color $col_light_selection

.details__info-icon
  circle(13px)
  display inline-block
  margin-right 7px
  color #fff
  font-weight bold
  font-size 10px;
  line-height 13px
  text-align center
  background-color $col_light_gray
  cursor pointer

.details__header-label
  color $col_primary;
  font-size 0.8125rem
  cursor pointer

.details__info-dropdown
  right 0
  left unset !important
  z-index 100
  width 345px
  padding 8px 15px 15px
  font-size 0.75rem
  line-height 1.5

  label
    margin-top 15px

    &:first-child
      margin-top 5px

.cog
  width 12px
  height 12px
  margin 0 4px
  padding-top 4px
  fill $col_medium_gray
</style>
