<template>
  <div
    class="uc-dropdown-input"
    :class="{ 'is-disabled': disabled, 'is-enabled': isEnabled && !disabled }"
    data-test="uc-dropdown-input"
    @click="toggle"
  >
    <transition name="slide">
      <label
        v-if="name"
        v-show="(selected || disabled)"
        v-truncate
        class="uc-dropdown-input__label"
      >{{ name }}</label>
    </transition>
    <input
      v-truncate
      class="uc-dropdown-input__field input"
      :class="{ 'is-selected': selected }"
      :disabled="disabled"
      type="text"
      :value="value"
      readonly
    >
    <span class="uc-dropdown-input__toggle">
      <i
        class="uc-dropdown-input__icon"
        :class="{ 'is-rotated': isEnabled }"
      >
        <chevron-down />
      </i>
    </span>
  </div>
</template>

<script>
import { ChevronDown16Thick as ChevronDown } from '../../../../icons'

export default {
  name: 'UcDropdownInput',
  components: {
    ChevronDown
  },
  props: {
    value: {
      type: String,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    },
    enabled: {
      type: Boolean,
      default: false
    },
    selected: {
      type: Boolean,
      default: false
    },
    name: {
      type: String,
      default: function () {
        return this.value
      }
    }
  },
  data () {
    return {
      isEnabled: false
    }
  },
  watch: {
    enabled: {
      handler (value) {
        this.isEnabled = value
      },
      immediate: true
    }
  },
  methods: {
    toggle () {
      if (this.disabled) {
        return
      }
      this.isEnabled = !this.isEnabled
      this.$emit('enable', this.isEnabled)
    }
  }
}
</script>

<style lang="stylus" scoped>
$input_height = 48px
$border = 1px solid $col_light_border
$space = 8px

.uc-dropdown-input__label
  secondary-petit-text()
  position absolute
  top -1 * $space
  left (2 * $space) - ($space / 4)
  z-index 1
  max-width 'calc(100% - %s)' % (4 * $space)
  padding 0 ($space / 4)

  &::before
    position absolute
    top 0
    left 0
    z-index -1
    display block
    width 100%
    height 50%
    background-color $ucommerce-white
    content ''

input.uc-dropdown-input__field
  position relative
  flex-shrink 1
  padding 13px $space * 2 15px
  background-color transparent
  cursor pointer
  pointer-events none
  secondary-body-text()
  stretch()

  &.is-selected
    primary-body-text()

.uc-dropdown-input__toggle
  display inline-block
  display flex
  flex-shrink 0
  align-items center
  justify-content center
  width $input_height
  height $input_height
  border-left $border

.uc-dropdown-input__icon
  width 2 * $space
  height 2 * $space
  transition transform 500ms

  &.is-rotated
    transform rotate(180deg)

.uc-dropdown-input
  position relative
  display flex
  align-items center
  justify-content space-between
  width 100%
  height $input_height
  border $border
  border-radius ($space / 2)
  background-color $ucommerce-white
  cursor pointer
  user-select none

  &.is-enabled
    border-bottom-right-radius 0
    border-bottom-left-radius 0

    .uc-dropdown-input__field
      primary-body-text-bold()

  &.is-disabled
    background-color $platform-bg
    cursor default
    user-select none

    .uc-dropdown-input__label
      background transparent

    .uc-dropdown-input__icon
      color $col_input_field_disabled_text

    .uc-dropdown-input__field
      color $col_input_field_disabled_text
      cursor default

.slide-enter-active
  transition transform 200ms ease-out

.slide-enter
  transform translate(0, 100%)
  opacity 1

.slide-leave-active
  transition opacity 250ms

.slide-leave-to
  opacity 0
</style>
