<template>
  <div class="content-navigator-grid-node">
    <uc-navigator-node
      :selected="selected"
      data-test="grid-node"
      @click="onSelect"
    >
      <div class="content-navigator-grid-node__container">
        <div class="content-navigator-grid-node__left-area">
          <span class="content-navigator-grid-node__icon">
            <content-branch-icon v-if="hasChildren" />
            <content-leaf-icon v-else />
          </span>
          <div
            v-truncate
            class="content-navigator-grid-node__name"
          >
            {{ node.name }}
          </div>
        </div>
        <div
          v-if="hasChildren"
          class="content-navigator-grid-node__right-area"
        >
          <div class="content-navigator-grid-node__children-count">
            {{ $tc('global.items', node.childrenCount, { count: node.childrenCount }) }}
          </div>
          <div class="content-navigator-grid-node__action">
            <uc-button-round
              theme="ghost"
              size="s"
              @click.stop="onOpen"
            >
              <chevron-forward />
            </uc-button-round>
          </div>
        </div>
      </div>
    </uc-navigator-node>
  </div>
</template>

<script>
import { UcNavigatorNode } from '@/shared/components/generic/uc-navigator'
import UcButtonRound from '@/shared/components/forms/uc-button-round'
import { ChevronForward16Thick as ChevronForward, ContentBranch24Thick as ContentBranchIcon, ContentLeaf24Thick as ContentLeafIcon } from '@/shared/icons'

const ComponentEvent = {
  OPEN: 'open',
  SELECT: 'select'
}

export default {
  name: 'ContentNavigatorGridNode',
  components: {
    UcNavigatorNode,
    ContentBranchIcon,
    ContentLeafIcon,
    UcButtonRound,
    ChevronForward
  },
  props: {
    node: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    hasChildren () {
      return this.node.childrenCount > 0
    }
  },
  methods: {
    onOpen () {
      this.$emit(ComponentEvent.OPEN, this.node)
    },
    onSelect () {
      this.$emit(ComponentEvent.SELECT, this.node)
    }
  }
}
</script>

<style lang="stylus" scoped>
.content-navigator-grid-node__container
  display flex
  flex 1
  justify-content space-between
  min-width 0
  height 6 * $base-unit
  padding $base-unit 2 * $base-unit
  border $base-border
  border-radius ($base-unit / 2)

.content-navigator-grid-node__left-area
  display flex
  flex 1
  align-items center
  min-width 0

.content-navigator-grid-node__name
  primary-petit-text()
  margin-left $base-unit
  color $primary-text

.content-navigator-grid-node__right-area
  display flex
  align-items center
  justify-content flex-end
  margin-left 2 * $base-unit

.content-navigator-grid-node__children-count
  primary-petit-text()
  color $secondary-text
  line-height inherit
  white-space nowrap

.content-navigator-grid-node__icon
  box(3 * $base-unit)

.content-navigator-grid-node__action
  margin-left (2 * $base-unit)
</style>
