<template>
  <article class="details-layout">
    <base-layout>
      <div class="details-layout__header">
        <uc-button
          data-test="back-button"
          :theme="buttonTheme"
          @click="onClick"
        >
          <template #before>
            <arrow-back />
          </template>
          {{ $tc('global.back') }}
        </uc-button>
        <!-- TODO: Remove link slot when no legacy link anymore -->
        <slot name="link" />
      </div>
      <slot />
    </base-layout>
  </article>
</template>

<script>
import UcButton, { ThemeProp } from '@/shared/components/forms/uc-button'
import { ArrowBack16Normal as ArrowBack } from '@/shared/icons'
import BaseLayout from './_base-layout.vue'

const ComponentEvent = {
  BACK_CLICK: 'back-click'
}

export default {
  name: 'DetailsLayout',
  components: {
    BaseLayout,
    UcButton,
    ArrowBack
  },
  computed: {
    buttonTheme () {
      return ThemeProp.LINK
    }
  },
  methods: {
    onClick () {
      window.scroll(0, 0)
      this.$emit(ComponentEvent.BACK_CLICK)
    }
  }
}
</script>

<style lang="stylus" scoped>
.details-layout
  padding-bottom 4 * $base-unit

.details-layout__header
  position sticky
  top $base-unit
  z-index $base-z-index * 1000
  display flex
  align-items center
  justify-content space-between
  margin-top $base-unit * 2
  cursor pointer
  pointer-events all
</style>
