<template>
  <use-navigator-data-provider :content-type="contentType">
    <template v-slot="{ data, getData, isLoading, isEmptyRootState }">
      <uc-image-picker
        v-bind="{ hostSystemName, isLoading, isEmptyRootState }"
        :nodes="data"
        :selected-nodes-guids="selected"
        full-height
        @openNode="getData"
        @selectNode="selectNode"
      />
    </template>
  </use-navigator-data-provider>
</template>

<script>
import UcImagePicker from '@/shared/components/generic/uc-image-picker/uc-image-picker'
import { UseNavigatorDataProvider } from '@/shared/components/generic/uc-navigator'
import { mapGetters } from 'vuex'

const ComponentEvent = {
  NODES_SELECTED: 'nodesSelected'
}

export default {
  name: 'ImagePickerSingleSelect',
  components: {
    UseNavigatorDataProvider,
    UcImagePicker
  },
  props: {
    selected: {
      type: Array,
      required: true
    },
    contentType: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      selectedNodes: []
    }
  },
  computed: {
    ...mapGetters(['systemInfo']),
    hostSystemName () {
      return this.systemInfo.cms
    }
  },
  methods: {
    selectNode (node) {
      this.selectedNodes = [node]
      this.$emit(ComponentEvent.NODES_SELECTED, this.selectedNodes)
    }
  }
}
</script>
