<template>
  <div class="products">
    <transition
      appear
      name="fade"
      mode="out-in"
      @after-enter="toggleSearchFocus"
      @after-leave="toggleSearchFocus"
    >
      <keep-alive>
        <products-search
          v-if="isNavigationSearch"
          :key="productSearchKey"
          v-bind="{ isSearchFocused, contentLanguage }"
          :list-id="searchListId"
          @open-entity-editor="openEntityEditor"
          @close-entity-editor="closeEntityEditor"
          @toggle-dialog="toggleDialog"
        />

        <products-navigate
          v-else
          v-bind="{ contentLanguage, displayedNode }"
          :list-id="navigateListId"
          @open-entity-editor="openEntityEditor"
          @close-entity-editor="closeEntityEditor"
          @toggle-dialog="toggleDialog"
        />
      </keep-alive>
    </transition>

    <!-- Create product dialog -->
    <create-product
      v-if="isDialogOpen"
      v-bind="{ contentLanguage, categoryGuid }"
      @hideDialog="toggleDialog"
      @addProductToListAndOpenItsDetails="addProductToListAndOpenDetails"
      @productCreated="onProductCreated"
    />

    <!-- Product details -->
    <overlay-modal v-if="hasDetails">
      <details-layout @back-click="onBackClick">
        <template #link>
          <a
            v-if="currentDetails.meta.productUrl"
            class="products__legacy-link"
            :href="currentDetails.meta.productUrl"
            target="_self"
            title="For missing features, click here to edit in old version"
          >
            Edit in old version
          </a>
        </template>
        <generic-details
          @deleted="deleteEntityAndCloseDetails"
          @openVariantList="openEntityEditor"
        />
      </details-layout>
    </overlay-modal>
  </div>
</template>

<script>
import GenericDetails from '@/catalogs/components/details-view/generic-details'
import OverlayModal from '@/shared/components/widgets/overlay-modal'
import DetailsLayout from '@/layouts/details-layout'
import ProductsSearch from './products-search'
import ProductsNavigate from './products-navigate'
import CreateProduct from '@/catalogs/components/generic-list/create-product'

import { createNamespacedHelpers } from 'vuex'

const builderStore = createNamespacedHelpers('builder')
const genericListStore = createNamespacedHelpers('genericList')
const detailsStore = createNamespacedHelpers('details')

const NavigationType = {
  SEARCH: 'Search',
  NAVIGATE: 'Navigate'
}

export default {
  name: 'Products',
  components: {
    ProductsSearch,
    GenericDetails,
    OverlayModal,
    DetailsLayout,
    CreateProduct,
    ProductsNavigate
  },
  props: {
    contentLanguage: {
      type: String,
      required: true
    },
    navigationType: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      isDialogOpen: false,
      isSearchFocused: false,
      productSearchKey: 0
    }
  },
  computed: {
    ...builderStore.mapGetters(['displayedNode']),
    ...genericListStore.mapGetters(['list']),
    ...detailsStore.mapGetters(['currentDetails']),
    searchListId () {
      return NavigationType.SEARCH
    },
    categoryGuid () {
      return this.isNavigationSearch ? null : this.displayedNode.guid
    },
    navigateListId () {
      return NavigationType.NAVIGATE
    },
    currentListId () {
      return this.navigationType
    },
    currentList () {
      return this.list(this.currentListId)
    },
    hasDetails () {
      return !!this.currentDetails
    },
    isNavigationSearch () {
      return this.navigationType === NavigationType.SEARCH
    }
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails', 'clearDetails']),
    ...genericListStore.mapMutations(['removeItemsFromList', 'setCurrentListCount', 'addItemsToBeginningOfList']),
    toggleDialog () {
      this.isDialogOpen = !this.isDialogOpen
    },
    deleteEntityAndCloseDetails (payloadObject) {
      const listId = payloadObject.isVariant ? 'VariantsList' : this.currentListId
      this.removeItemsFromList({ listId, itemGuids: [ payloadObject.guid ] })
      this.closeEntityEditor()
    },
    openEntityEditor (row) {
      this.setDetails({ details: row })
    },
    closeEntityEditor () {
      this.clearDetails()
    },
    onProductCreated () {
      // Force product-search to rerender to display the newly added product: https://michaelnthiessen.com/key-changing-technique/
      this.productSearchKey++
      this.setCurrentListCount({ id: this.currentListId, newCount: this.currentList.listCount + 1 })
    },
    addProductToListAndOpenDetails (row) {
      this.addItemsToBeginningOfList({ id: this.currentListId, newItems: [row] })
      this.openEntityEditor(row)
    },
    onBackClick () {
      this.closeEntityEditor()
    },
    toggleSearchFocus () {
      this.isSearchFocused = !this.isSearchFocused
    }
  }
}
</script>

<style lang="stylus" scoped>
.products__legacy-link
  text-decoration none
  user-select none
  primary-details-text()

  &:hover
    text-decoration underline
</style>
