<template>
  <div
    class="uc-dropdown"
    data-test="uc-dropdown"
  >
    <div
      v-click-outside="onClickOutside"
      class="uc-dropdown__wrapper"
    >
      <div
        v-if="isPending"
        class="uc-dropdown__pending"
      >
        <slot name="pending" />
      </div>
      <uc-dropdown-input
        v-if="!isEmpty"
        :value="placeholder"
        :disabled="readonly"
        :enabled="shouldShowList"
        :selected="hasSelected"
        :name="name"
        @enable="onToggle"
      />
      <div
        v-if="isEmpty"
        class="uc-dropdown__empty-state--no-options"
      >
        <div class="uc-dropdown__empty-state__text">
          {{ $t('global.noOptionsFound') }}
        </div>
      </div>
      <transition
        name="dropdown"
      >
        <div
          v-show="shouldShowList"
          class="uc-dropdown__container"
        >
          <template v-if="isLongList">
            <div class="uc-dropdown__search">
              <use-fuzzy-search
                :keys="searchKeys"
                :list="options"
                :threshold="0.4"
                @search="onSearch"
                @search-input-changed="onSearchChanged"
              >
                <template v-slot="{ value, setValue }">
                  <uc-search
                    dark
                    dense
                    :value="value"
                    :focus="shouldShowList"
                    :clear="shouldShowList"
                    @input="setValue($event, value)"
                  />
                </template>
              </use-fuzzy-search>
            </div>
            <div
              v-if="!hasSearchResults"
              class="uc-dropdown__no-results"
            >
              <p>{{ $t('global.searchNoResults') }}</p>
            </div>
          </template>
          <ul
            v-if="hasSearchResults"
            v-custom-scroll-bar
            class="uc-dropdown__list"
          >
            <template v-for="option in searchResults">
              <li
                v-if="option.label"
                :key="option.guid"
                class="uc-dropdown__item"
                data-test="uc-dropdown-item"
              >
                <slot v-bind="{ option, searchTerm }" />
              </li>
            </template>
          </ul>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import UcSearch from '../../forms/uc-search'
import UcDropdownInput from './uc-dropdown-input'
import UseFuzzySearch from '../../wrappers/use-fuzzy-search'
import ClickOutside from 'vue-click-outside'

export default {
  name: 'UcDropdown',
  components: {
    UcSearch,
    UcDropdownInput,
    UseFuzzySearch
  },
  directives: {
    ClickOutside
  },
  props: {
    options: {
      validator: prop => Array.isArray(prop) || prop === undefined,
      required: true
    },
    selected: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: function () {
        return this.$t('global.selectPlaceholder')
      }
    },
    collapse: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    },
    name: {
      type: String,
      default: function () {
        return this.placeholder
      }
    }
  },
  data () {
    return {
      shouldShowList: false,
      searchKeys: ['label'],
      searchResults: this.options,
      searchTerm: '',
      hasSelected: false
    }
  },
  computed: {
    hasOptions () {
      return !!this.options && this.options.length >= 0
    },
    isLongList () {
      return !!this.options && this.options.length > 6
    },
    isEmpty () {
      return !!this.options && this.options.length === 0
    },
    hasSearchResults () {
      return !this.searchResult || this.searchResults.length > 0
    },
    isPending () {
      return !this.hasOptions
    }
  },
  watch: {
    selected: {
      handler (newValue) {
        this.hasSelected = newValue
      },
      immediate: true
    },
    collapse (value) {
      this.shouldShowList = !value
    },
    options () {
      this.searchResults = this.options
    }
  },
  methods: {
    onToggle (enabled) {
      this.shouldShowList = enabled
      this.$emit('toggle', this.shouldShowList)
    },
    onSearch (results) {
      this.searchResults = results
    },
    onSearchChanged (result) {
      this.searchTerm = result
    },
    onClickOutside () {
      this.shouldShowList = false
    }
  }
}
</script>

<style lang="stylus" scoped>
$space = 8px
$long_list_height = 230px

.uc-dropdown
  position relative
  width 100%
  user-select none

.uc-dropdown__container
  position absolute
  z-index 3
  width 100%
  border 1px solid $col_light_border
  border-top none
  border-radius 0 0 ($space / 2) ($space / 2)
  background-color $ucommerce-white

.uc-dropdown__list
  max-height $long_list_height
  margin 0
  padding $space * 2 0
  overflow-y scroll
  white-space wrap
  list-style none
  cursor pointer

.uc-dropdown__item
  display flex
  align-items center
  height 4 * $space
  padding 0 $space * 3

  &:not(:last-child)
    margin-bottom $space

  &:hover
    background-color $col_pale_gray

  &:active
    background-color $col_pale_lilac

.uc-dropdown__search
  padding (2 * $space) (2 * $space) 0

.uc-dropdown__no-results
  secondary-body-text()
  display flex
  align-items center
  justify-content center
  height $long_list_height
  color $col_light_border
  font-size 1.5em
  text-align center

.uc-dropdown__empty-state__text
  padding-left 16px
  color $ucommerce-secondary
  secondary-body-text()
  user-select none

.uc-dropdown__empty-state--no-options
  display flex
  align-items center
  width 100%
  height 48px
  border 1px $default-border-color solid
  border-radius 4px
  background $platform-bg
  cursor not-allowed

.uc-dropdown__empty-state--loading
  display flex
  align-items center
  justify-content center;
  width 100%
  height 48px
  border 1px $default-border-color solid
  border-radius 4px

.uc-dropdown__pending
  position absolute
  top 0
  right 0
  bottom 0
  left 0
  z-index 100
  display flex
  align-items center
  justify-content center
  background-color rgba(255, 255, 255, 0.5)
</style>
