<template>
  <div
    class="uc-text-input"
    data-test="uc-text-input"
  >
    <input-group
      v-bind="{ label, readonly, hasErrors }"
    >
      <input
        ref="input"
        v-model="inputValue"
        type="text"
        :placeholder="label"
        :disabled="readonly"
        data-test="uc-text-input-input"
        @input="onInput"
        @blur="onBlur"
      >
    </input-group>
  </div>
</template>

<script>
import InputGroup from './input-group'

export default {
  name: 'UcTextInput',
  components: {
    InputGroup
  },
  props: {
    label: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    readonly: {
      type: Boolean,
      default: false
    },
    hasErrors: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      inputValue: ''
    }
  },
  watch: {
    value: {
      immediate: true,
      handler (value) {
        this.inputValue = value
      }
    }
  },
  methods: {
    onBlur (event) {
      this.$emit('blur', event)
    },
    onInput (event) {
      this.$emit('input', event.target.value)
    },
    focus () {
      this.$nextTick(() => this.$refs.input.focus())
    }
  }
}
</script>
