<template>
  <div class="uc-text-area">
    <input-group
      v-bind="{ label, readonly, hasErrors }"
    >
      <textarea
        v-model="inputValue"
        :disabled="readonly"
        :readonly="readonly"
        rows="5"
        :placeholder="label"
        @input="onInput"
        @blur="onBlur"
      />
    </input-group>
  </div>
</template>

<script>
import InputGroup from './input-group'

export default {
  name: 'UcTextArea',
  components: {
    InputGroup
  },
  props: {
    label: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    hasErrors: {
      type: Boolean,
      default: false
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      inputValue: ''
    }
  },
  watch: {
    value: {
      immediate: true,
      handler (value) {
        this.inputValue = value
      }
    }
  },
  methods: {
    onBlur (event) {
      this.$emit('blur', event)
    },
    onInput (event) {
      this.$emit('input', event.target.value)
    }
  }
}
</script>
