<template>
  <div
    class="uc-price-editor"
    :class="cssClassObject"
  >
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="compactValue"
      :required="isRequired"
    >
      <div class="uc-price-editor__input">
        <input-group
          v-bind="{ label, hasErrors }"
          :readonly="isReadonly"
        >
          <currency-input
            ref="currencyInput"
            v-model="inputValue"
            :currency="null"
            :locale="language"
            :precision="4"
            @change="onBlur"
          />
        </input-group>
        <span class="uc-price-editor__addon input-group-append">
          <abbr class="input-group-append__text">{{ currency }}</abbr>
        </span>
      </div>
    </editor-wrapper>
  </div>
</template>

<script>
import InputGroup from '@/shared/components/forms/input-group'
import editorMixin from '../editor-mixin'
import { CurrencyInput } from 'vue-currency-input'

export default {
  name: 'PriceEditor',
  components: {
    InputGroup,
    CurrencyInput
  },
  mixins: [ editorMixin ],
  data () {
    return {
      inputValue: null
    }
  },
  computed: {
    cssClassObject () {
      return {
        'uc-price-editor--readonly': this.isReadonly
      }
    },
    currency () {
      return this.definition.currencyIsoCode
    },
    formattedPrice () {
      const formattingOptions = {
        style: 'currency',
        currency: this.currency,
        maximumFractionDigits: 4
      }

      return new Intl.NumberFormat(this.language, formattingOptions).format(this.value)
    },
    compactValue () {
      return this.value ? this.formattedPrice : ''
    }
  },
  watch: {
    value: {
      immediate: true,
      handler (newValue) {
        this.inputValue = newValue > 0 ? newValue : null
      }
    }
  },
  mounted () {
    if (this.compact) {
      return
    }

    this.$refs.currencyInput.$el.placeholder = this.placeholder
  },
  methods: {
    onBlur (newPrice) {
      const newValue = newPrice || undefined
      const oldValue = this.value || undefined
      const isSameValue = oldValue === newValue || ((oldValue === undefined || oldValue === null) && newValue === '')

      if (isSameValue && !this.hasErrors) {
        return
      }

      this.onChange({
        newValue,
        oldValue,
        priceGroupGuid: this.guid,
        minimumQuantity: 1
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
$space = 8px

.uc-price-editor__input
  position relative

.input-group-append
  position absolute
  top 0
  right 0
  display flex
  justify-content center
  width 10 * $space
  height 100%
  border-left 1px solid $col_lightest_gray
  primary-body-text()
  user-select none

.input-group-append__text
  display inline-block
  padding 13px 16px 15px

.uc-price-editor--readonly .input-group-append
  secondary-body-text()
</style>
