<template>
  <fields-section
    paddingless
    @click="$emit('click')"
  >
    <uc-content-editor-single
      :definition="definition"
      :content-type="contentType"
      :name="name"
      @change="change"
    />
  </fields-section>
</template>

<script>
import FieldsSection from './fields-section'
import { UcContentEditorSingle } from '@/shared/components/editors/uc-content-picker-editor'

const ComponentEvent = {
  CHANGE: 'change'
}

export default {
  name: 'ContentEditorSingleSection',
  components: {
    FieldsSection,
    UcContentEditorSingle
  },
  props: {
    definition: {
      type: Object,
      required: true
    },
    contentType: {
      type: String,
      default: 'content'
    },
    name: {
      type: String,
      required: true
    }
  },
  methods: {
    change (event) {
      this.$emit(ComponentEvent.CHANGE, event)
    }
  }
}
</script>
