<template>
  <div class="products-search">
    <!-- Product search -->
    <uc-search
      v-model.trim="searchTerm"
      :focus="isSearchFocused"
    />

    <!-- Search results -->
    <div class="products-search__results">
      <fields-section paddingless>
        <list
          :id="listId"
          v-bind="{ nodeType, dataUrl, defaultColumnsConfiguration }"
          :header="$tc('list.products')"
          @sortBy="onSortBy"
          @openEntityEditor="openEntityEditor"
        >
          <template #actionsHeader>
            <span
              class="products-search__button"
            >
              <uc-button
                size="sm"
                @click="toggleDialog"
              >
                {{ $t('list.newButton') }}
              </uc-button>
            </span>
          </template>
        </list>
      </fields-section>
    </div>
  </div>
</template>

<script>
import List from '@/catalogs/components/generic-list/list'
import FieldsSection from '@/shared/components/sections/fields-section'
import UcSearch from '@/shared/components/forms/uc-search'
import UcButton from '@/shared/components/forms/uc-button'

import setUrlParam from '@/shared/js/set-url-param'
import { EntityType } from '@/shared/js/constants'

export default {
  name: 'ProductsSearch',
  components: {
    FieldsSection,
    List,
    UcSearch,
    UcButton
  },
  props: {
    listId: {
      type: String,
      required: true
    },
    contentLanguage: {
      type: String,
      required: true
    },
    isSearchFocused: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      searchTerm: '',
      sortBy: ''
    }
  },
  computed: {
    nodeType () {
      return EntityType.PRODUCT
    },
    defaultColumnsConfiguration () {
      return {
        'primaryImage': {
          'width': '90px'
        },
        'sku': {},
        'name': {
          'width': '250px'
        },
        'definition': {},
        'eur 15 pct': {
          'width': '360px'
        }
      }
    },
    dataUrl () {
      let url = `/products/${this.contentLanguage}`
      url = setUrlParam(url, 'searchTerm', this.searchTerm)
      url = setUrlParam(url, 'sort', this.sortBy)

      return url
    }
  },
  methods: {
    onSortBy (sortBy) {
      this.sortBy = sortBy
    },
    openEntityEditor (row) {
      this.$emit('open-entity-editor', row)
    },
    closeEntityEditor () {
      this.$emit('close-entity-editor')
    },
    toggleDialog () {
      this.$emit('toggle-dialog')
    }
  }
}
</script>

<style lang="stylus" scoped>
.products-search__results
  margin-top 4 * $base-unit

.products-search__button
  margin-right 2 * $base-unit
</style>
