import Vue from 'vue'
import Router from 'vue-router'
import { LayoutType } from '@/layouts'

// Views
import Catalogs from './views/catalogs.vue'
import Stores from './views/stores.vue'
import Store from './views/store.vue'
import CreateStore from '@/stores/views/create-store.vue'
import Products from './views/products'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/catalogs',
      name: 'catalogs',
      component: Catalogs
    },
    {
      path: '/stores',
      name: 'stores',
      component: Stores,
      meta: {
        layout: LayoutType.BASIC
      },
      children: [
        {
          path: 'new',
          name: 'create-store',
          component: CreateStore
        },
        {
          path: ':guid',
          name: 'store',
          props: true,
          component: Store
        }
      ]
    },
    {
      path: '/products',
      name: 'products',
      component: Products,
      props: true,
      meta: {
        layout: LayoutType.NAVIGATION
      }
    }
  ]
})
