<template>
  <label
    class="uc-checkbox"
    :class="{ readonly }"
    data-test="uc-checkbox"
  >
    <input
      v-show="!compact"
      :id="id"
      ref="input"
      type="checkbox"
      :name="id"
      :disabled="readonly"
      :checked="checkedValue"
      :value="guid"
      @change="onChange"
    >
    <span
      class="uc-checkbox__check"
      :class="{ compact }"
    >
      <check-dual v-if="dual" />
      <check v-else />
    </span>
    <span
      v-if="!compact && label"
      v-truncate
      class="uc-checkbox__label"
    >
      <slot>{{ label }}</slot>
    </span>
  </label>
</template>

<script>
import Check from '@/shared/icons/legacy/checkbox-checked.vue'
import CheckDual from '@/shared/icons/legacy/check-dual.vue'
import getId from '@/shared/js/id-generator.js'

export default {
  name: 'UcCheckbox',
  components: {
    Check,
    CheckDual
  },
  props: {
    label: {
      type: String,
      default: ''
    },
    readonly: {
      type: Boolean,
      default: false
    },
    checked: {
      type: Boolean,
      default: false
    },
    guid: {
      type: String,
      default: ''
    },
    indeterminate: {
      type: Boolean,
      default: false
    },
    dual: {
      type: Boolean,
      default: false
    },
    compact: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    checkedValue () {
      return this.checked ? 'checked' : ''
    },
    id () {
      return getId()
    }
  },
  watch: {
    indeterminate () {
      this.$refs.input.indeterminate = this.indeterminate
    }
  },
  methods: {
    onChange (event) {
      this.$emit('change', { value: event.target.checked, label: this.label, guid: this.guid })
    }
  }
}
</script>

<style lang="stylus" scoped>
.uc-checkbox
  position relative
  display flex
  align-items center
  width 100%
  font-size $input_font_size
  cursor pointer
  user-select none

  &.readonly
    cursor not-allowed

  .uc-checkbox__label
    width 100%
    margin-left 8px
    primary-details-text()

  .uc-checkbox__check
    display flex

    .compact &
      margin-top -9px
      margin-left 6px

    svg
      margin auto
      object-fit contain

    .readonly &
      opacity .6

  input[type=checkbox]
    position absolute
    display none

  input[type=checkbox]:not(:checked)
    & ~ .uc-checkbox__check svg
      /deep/ svg
        display none
</style>
