<template>
  <uc-navigator
    v-bind="{ nodes, isLoading, isEmptyRootState, navigation}"
    :no-items-title="$t('ucImagePicker.noMediaTitle')"
    :no-items-text="$t('ucImagePicker.noMediaText', { name: hostSystemName })"
    @navigate="navigate"
  >
    <template #emptyFolder>
      <div class="uc-image-picker__empty-folder">
        <h2 class="uc-image-picker__empty-folder__title">
          {{ $t('global.emptyFolder') }}
        </h2>
        <div
          class="uc-image-picker__empty-folder__back"
          @click="navigateBack"
        >
          <arrow-back />
          <span class="uc-image-picker__empty-folder__back-label">{{ $t('global.back') }}</span>
        </div>
      </div>
    </template>

    <template #actions>
      <div class="uc-image-picker__actions">
        <slot name="actions" />
      </div>
    </template>

    <template #content>
      <uc-navigator-view-grid :nodes="nodes">
        <template v-slot="{item}">
          <!-- Node -->
          <component
            :is="getComponentByNodeType(item.pickerNodeType)"
            :node="item"
            :selected="isNodeSelected(item.guid)"
            @select="onSelect"
            @open="onOpen"
          />
        </template>
      </uc-navigator-view-grid>
    </template>
  </uc-navigator>
</template>

<script>
import { ArrowBack16Normal as ArrowBack } from '@/shared/icons'
import Constants from '@/shared/js/constants'
import UcImagePickerGridImage from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/components/uc-image-picker-grid-image'
import UcImagePickerGridFolder from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/components/uc-image-picker-grid-folder'
import UcImagePickerGridFile from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/components/uc-image-picker-grid-file'

import {
  UcNavigator,
  UcNavigatorViewGrid
} from '@/shared/components/generic/uc-navigator'

const ComponentEvent = {
  SELECT_NODE: 'selectNode',
  OPEN_NODE: 'openNode'
}

const NodeTypeToComponentMap = {
  [Constants.NodeType.Image]: UcImagePickerGridImage,
  [Constants.NodeType.Folder]: UcImagePickerGridFolder,
  [Constants.NodeType.File]: UcImagePickerGridFile
}

export default {
  name: 'UcImagePicker',
  components: {
    ArrowBack,
    UcNavigator,
    UcNavigatorViewGrid
  },
  props: {
    nodes: {
      type: Array,
      required: true
    },
    isEmptyRootState: {
      type: Boolean,
      default: false
    },
    selectedNodesGuids: {
      type: Array,
      default: () => []
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    hostSystemName: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      navigation: []
    }
  },
  mounted () {
    this.navigation.push({
      name: 'Media',
      guid: -1,
      item: null
    })
  },
  methods: {
    isNodeSelected (guid) {
      return this.selectedNodesGuids.includes(guid)
    },
    getComponentByNodeType (nodeType) {
      return NodeTypeToComponentMap[nodeType.toLowerCase()]
    },
    openNode (node) {
      this.$emit(ComponentEvent.OPEN_NODE, node)
    },
    navigate (node) {
      node.guid === -1 ? this.navigateToRootNode() : this.navigateToNode(node)
    },
    navigateBack () {
      if (this.navigation.length > 1) {
        this.navigate(this.navigation[this.navigation.length - 2])
      }
    },
    navigateToRootNode () {
      this.openNode(null)
      this.navigation = [this.navigation[0]]
    },
    navigateToNode (node) {
      const indexOf = this.navigation.indexOf(node)
      this.openNode(node.item)
      this.navigation.splice(indexOf + 1, this.navigation.length)
    },
    onSelect (node) {
      this.$emit(ComponentEvent.SELECT_NODE, node)
    },
    onOpen (node) {
      this.openNode(node)
      this.navigation.push({
        name: node.name,
        guid: node.id,
        item: node
      })
    }
  }
}

</script>

<style scoped lang="stylus">
.uc-image-picker__empty-folder
  width 100%
  text-align center

.uc-image-picker__empty-folder__title
  primary-heading-2()

.uc-image-picker__empty-folder__back-label
  primary-details-text()
  margin-left 6px

.uc-image-picker__empty-folder__back
  display flex
  align-items center
  justify-content center
  cursor pointer

.uc-image-picker__view-switcher
  display flex
  justify-content flex-end
  padding-top 8px
  padding-bottom 8px

.uc-image-picker__actions
  display flex
  flex-direction row

.uc-image-picker__view-switcher__icon
  padding 4px
  cursor pointer
</style>
