import * as types from './mutation-types'
import { distinctByEntityGuid } from '../../../shared/js/distinct'
import Helpers from '../../../shared/js/generic-list-helpers'

const mutations = {
  [types.ADD_LIST_ID] (state, id) {
    state.lists[id] = {
      id: id,
      localStorageKey: null,
      columns: [],
      rows: [],
      listCount: -1

    }
  },
  [types.SET_LIST_LOCAL_STORAGE_KEY] (state, { id, key }) {
    state.lists[id].localStorageKey = key
  },
  [types.SET_LIST_COLUMNS] (state, { id, columns, defaultColumnNames = [], initDefaultColumns = false }) {
    let currentList = state.lists[id]
    currentList.columns = columns
    Helpers.saveColumnOrder(currentList)

    if (initDefaultColumns) {
      Helpers.setDefaultColumnOrder(currentList, defaultColumnNames, state.defaultColumnsCount)
    }
  },
  [types.SET_LIST_ROWS] (state, { id, rows }) {
    state.lists[id].rows = rows
  },
  [types.REMOVE_ITEMS_FROM_LIST] (state, { listId, itemGuids }) {
    const filteredList = state.lists[listId].rows.filter(x => !itemGuids.includes(x.meta.entityGuid))
    const currentList = state.lists[listId]

    currentList.rows = filteredList
    currentList.listCount -= itemGuids.length
  },
  [types.ADD_LIST_ROWS] (state, { id, rows }) {
    state.lists[id].rows = distinctByEntityGuid(state.lists[id].rows.concat(rows))
  },
  [types.TOGGLE_LIST_COLUMN] (state, { id, index, show }) {
    state.lists[id].columns.find(x => x.index === index).show = show
    Helpers.saveColumnOrder(state.lists[id])
  },
  [types.TOGGLE_LIST_COLUMNS] (state, { id, columnNames }) {
    let currentList = state.lists[id]
    currentList.columns.filter(x => columnNames.includes(x.key)).forEach(x => { x.show = true })
    currentList.columns.filter(x => !columnNames.includes(x.key)).forEach(x => { x.show = false })
    Helpers.saveColumnOrder(currentList)
  },
  [types.SET_CURRENT_LIST_COUNT] (state, { id, newCount }) {
    state.lists[id].listCount = newCount
  },
  [types.ADD_ITEMS_TO_BEGINNING_OF_LIST] (state, { id, newItems }) {
    state.lists[id].rows = [...newItems, ...state.lists[id].rows]
  }
}

export default mutations
