<template>
  <div class="uc-long-text">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="value"
      :required="isRequired"
    >
      <uc-text-area
        v-bind="{ value, label, placeholder, hasErrors }"
        :readonly="isReadonly"
        :tooltip="description"
        @blur="onBlur"
      />
    </editor-wrapper>
  </div>
</template>

<script>
import UcTextArea from '../forms/uc-text-area'
import editorMixin from './editor-mixin'

export default {
  name: 'UcLongText',
  components: {
    UcTextArea
  },
  mixins: [ editorMixin ],
  methods: {
    onBlur (e) {
      const newValue = e.target.value
      const oldValue = this.definition.value
      const isSameValue = oldValue === newValue || ((oldValue === undefined || oldValue === null) && newValue === '')

      if (isSameValue && !this.hasErrors) {
        return
      }

      this.onChange({
        newValue,
        oldValue
      })
    }
  }
}
</script>
