<template>
  <div class="uc-numeric-input">
    <input-group
      v-bind="{ label, readonly, hasErrors }"
    >
      <input
        v-model="inputValue"
        type="number"
        pattern="[0-9]{10}|"
        :disabled="readonly"
        :placeholder="label"
        @input="onInput"
        @blur="onBlur"
      >
    </input-group>
  </div>
</template>

<script>
import InputGroup from './input-group'
import debounce from '@/shared/js/debounce'

export default {
  name: 'UcNumericInput',
  components: {
    InputGroup
  },
  props: {
    label: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    value: {
      type: Number,
      default: null
    },
    readonly: {
      type: Boolean,
      default: false
    },
    hasErrors: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      inputValue: undefined
    }
  },
  watch: {
    value: {
      immediate: true,
      handler (value) {
        this.inputValue = value
      }
    }
  },
  methods: {
    onBlur (event) {
      this.$emit('blur', event)
    },
    onInput: debounce(function (event) {
      this.$emit('input', event.target.value)
    }, 250)
  }
}
</script>

<style lang="stylus" scoped>
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button
  margin 0
  appearance none
</style>
