import mapMedia from '@/catalogs/js/map-media-response'
import mapCategories from '@/catalogs/js/map-category-response'
import constants from '@/shared/js/constants'
import { validateAgainstEnum } from '@/shared/js/validate-against'

const ContentTypeProp = {
  MEDIA: 'media',
  CONTENT: 'content'
}

export default {
  name: 'UseNavigatorDataProvider',
  props: {
    contentType: {
      type: String,
      validate: validateAgainstEnum(ContentTypeProp),
      required: true
    }
  },
  data () {
    return {
      data: [],
      isLoading: false,
      isEmptyRootState: false,
      folderCount: undefined
    }
  },
  mounted () {
    this.getData(null)
  },
  methods: {
    async getData (node) {
      const endpoint = node
        ? `${this.contentType}/${node.nodeType}/${node.guid}`
        : `${this.contentType}/root`
      this.isLoading = true
      const result = await this.axios.get(endpoint)

      if (node === null && result.data.length === 0) {
        this.isEmptyRootState = true
        this.isLoading = false
      } else {
        this.folderCount = +result.headers['x-total-count']
        const mappedResult = mapMedia(mapCategories(result.data))
        this.data = this.sortArrayByNodeType(mappedResult)
        this.isLoading = false
      }

      this.$emit('emptyRootState', this.isEmptyRootState)
    },
    sortArrayByNodeType (array) {
      // If contentType is 'content', all nodeTypes = 'content', so it does not make sense to perform any sort
      if (this.contentType === ContentTypeProp.CONTENT) {
        return array
      }

      const folders = array.filter(item => item.nodeType.toLowerCase() === constants.NodeType.Folder || item.nodeType.toLowerCase() === constants.NodeType.Album)
      const files = array.filter(item => item.nodeType.toLowerCase() === constants.NodeType.File)
      const images = array.filter(item => item.nodeType.toLowerCase() === constants.NodeType.Image)

      return [ ...folders, ...files, ...images ]
    }
  },
  render () {
    return this.$scopedSlots.default({
      data: this.data,
      getData: this.getData,
      isLoading: this.isLoading,
      isEmptyRootState: this.isEmptyRootState
    })
  }
}
