<template>
  <transition name="target">
    <div
      v-show="show"
      class="section__drop-target"
      @drop.prevent.stop="drop"
      @dragover.prevent="dragover"
    />
  </transition>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const { mapGetters } = createNamespacedHelpers('builder')
export default {
  name: 'DropTarget',
  props: {
    sortOrder: {
      type: Number,
      default: 0
    },
    targetIndex: {
      type: Number,
      default: 0
    },
    visibleIndex: {
      type: Number,
      default: 0
    },
    sectionGuid: {
      type: String,
      default: ''
    }
  },
  computed: {
    ...mapGetters(['dragInfo']),
    show () {
      return this.targetIndex === this.visibleIndex && this.dragInfo.currentSectionGuid === this.sectionGuid
    }
  },
  methods: {
    drop () {
      this.$emit('dropped', this.sortOrder)
    },
    dragover (e) {
      e.dataTransfer.dropEffect = 'move'
    }
  }
}
</script>

<style lang="stylus" scoped>
.section__drop-target
  height $treenode_height
  border dashed 1px rgba(0,0,0,0.3)
  border-radius $treenode_border_radius
</style>
