<template>
  <div
    class="store-navigator"
    data-test="store-navigator"
  >
    <uc-navigator
      v-bind="{ nodes, scrollTo, disableInfiniteScroll }"
      :is-loading="loading"
      @requestNextPage="onRequestNextPage"
    >
      <template #content="contentProps">
        <div class="store-navigator__view">
          <uc-navigator-view-grid-spacious
            :nodes="contentProps.nodes"
            grid-gap="lg"
          >
            <template #default="{ item }">
              <store-navigator-leaf-node
                :node="item"
                :size="nodeSize"
                @click="onClick(item)"
              />
            </template>
          </uc-navigator-view-grid-spacious>
        </div>
      </template>
      <template #content-footer>
        <div class="store-navigator__loading">
          <loading-indicator is-loading />
        </div>
      </template>
    </uc-navigator>
  </div>
</template>

<script>
import { UcNavigator, UcNavigatorViewGridSpacious } from '@/shared/components/generic/uc-navigator'
import StoreNavigatorLeafNode, { SizeProp } from './components/store-navigator-leaf-node'
import LoadingIndicator from '@/shared/components/widgets/loading-indicator'

const ComponentEvent = {
  SELECT: 'select',
  REQUEST_NEXT_PAGE: 'requestNextPage'
}

export default {
  name: 'StoreNavigator',
  components: {
    UcNavigator,
    StoreNavigatorLeafNode,
    UcNavigatorViewGridSpacious,
    LoadingIndicator
  },
  props: {
    nodes: {
      type: Array,
      required: true
    },
    scrollTo: {
      type: Array,
      default: () => [],
      validator: (value) => value.length <= 2 && value.every(entry => Number.isInteger(entry))
    },
    disableInfiniteScroll: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      nodeSize: this.nodes.length > 4 ? SizeProp.MD : SizeProp.LG
    }
  },
  methods: {
    onClick (item) {
      this.$emit(ComponentEvent.SELECT, item)
    },
    onRequestNextPage () {
      this.$emit(ComponentEvent.REQUEST_NEXT_PAGE)
    }
  }
}
</script>

<style lang="stylus" scoped>
.store-navigator
  width 100%
  height 100%

.store-navigator__view
  margin 3 * $base-unit

.store-navigator__loading
  display flex
  align-items center
  justify-content center
  width 100%
  height 4 * $base-unit
  padding-bottom 3 * $base-unit
</style>
