<template>
  <use-selection
    v-model="selection"
    :items="items"
  >
    <template #default="{ isSelected, toggle }">
      <div
        class="uc-list-selectable"
        :class="cssClassObject"
      >
        <!-- List -->
        <div class="uc-list-selectable__list">
          <uc-list v-bind="{ items }">
            <template #default="{ item }">
              <!-- List item -->
              <uc-list-item
                :key="item[itemKey]"
                actionable
                @click="toggle(item)"
              >
                <div class="uc-list-selectable__list__item">
                  <div class="uc-list-selectable__list__item__toggle">
                    <uc-checkbox
                      :checked="isSelected(item)"
                      data-test="list-toggle"
                    />
                  </div>
                  <div class="uc-list-selectable__list__item__content">
                    <slot :item="item" />
                  </div>
                </div>
              </uc-list-item>
            </template>
          </uc-list>
        </div>
        <!-- Actions -->
        <transition name="fade-in-up">
          <div
            v-if="hasActions"
            v-show="shouldShowActions"
            class="uc-list-selectable__actions"
            data-test="list-actions"
          >
            <slot
              name="actions"
              v-bind="{ selection }"
            />
          </div>
        </transition>
      </div>
    </template>
  </use-selection>
</template>

<script>
import UcList, { UcListItem } from '../uc-list'
import UcCheckbox from '../../forms/uc-checkbox'
import UseSelection from '../../wrappers/use-selection'

const ComponentEvent = {
  INPUT: 'input'
}

export default {
  name: 'UcListSelectable',
  components: {
    UseSelection,
    UcList,
    UcListItem,
    UcCheckbox
  },
  props: {
    items: {
      type: Array,
      required: true
    },
    itemKey: {
      type: [String, Number],
      required: true
    },
    value: {
      type: [Array, Boolean],
      default: false
    }
  },
  data () {
    return {
      internalValue: []
    }
  },
  computed: {
    selection: {
      get () {
        return this.value ? this.value : this.internalValue
      },
      set (newValue) {
        if (this.value) {
          this.$emit(ComponentEvent.INPUT, newValue)
        } else {
          this.internalValue = newValue
        }
      }
    },
    cssClassObject () {
      return {
        'uc-list-selectable--with-actions': this.hasActions
      }
    },
    hasActions () {
      return !!this.$scopedSlots.actions
    },
    shouldShowActions () {
      return this.hasActions && this.selection.length > 0
    }
  }
}
</script>
<style lang="stylus" scoped>
.uc-list-selectable
  position relative
  display flex
  flex 1 1 auto
  flex-direction column
  height 100%

.uc-list-selectable__list__item
  display flex
  align-items center

.uc-list-selectable__list__item__toggle
  margin-right $base-unit * 3
  pointer-events none

.uc-list-selectable__list__item__content
  flex-grow 1

.uc-list-selectable__actions
  position sticky
  right 0
  bottom 0
  left 0
  display flex
  align-items center
  justify-content center
  padding $base-unit * 2 0
  pointer-events  none

.uc-list-selectable--with-actions
  .uc-list-selectable__list
    flex 1 1 auto
    height 100%
    overflow-y auto
</style>
