<template>
  <div class="relations-list">
    <list
      v-show="!isLoading && hasRows"
      v-bind="{ id, dataUrl, defaultColumnsConfiguration, listType }"
      :header="$tc('relationsList.header')"
      node-type="product"
      selectable-rows
      @sortBy="onSortBy"
      @initialLoadStarted="onInitialLoadStarted"
      @initialLoadFinished="onInitialLoadFinished"
    >
      <!-- Add button -->
      <template #actionsHeader>
        <uc-button
          size="sm"
          @click="showModal = true"
        >
          {{ $tc('relationsList.new') }}
        </uc-button>
      </template>

      <!-- Notifications -->
      <template #notifications>
        <template v-if="notifications.length > 0">
          <div
            v-for="(notification, index) in notifications"
            :key="index"
            class="relations-list__snackbar"
          >
            <uc-snackbar
              anchor="top"
              v-bind="notification"
              @click="clearNotification(index)"
              @hide="clearFiniteNotifications"
            />
          </div>
        </template>
      </template>

      <!-- Remove button -->
      <template
        #actionsFooter="{ selected, clearSelected }"
      >
        <transition name="fade-in-up">
          <uc-button
            v-show="selected.length"
            floating
            @click="onClick(selected, clearSelected)"
          >
            {{ $tc('global.remove') }}
          </uc-button>
        </transition>
      </template>
    </list>
    <div
      v-show="!isLoading && !hasRows"
      class="relations-list__empty-state"
    >
      <empty-state
        :button-text="$tc('relationsList.new')"
        @click="showModal = true"
      >
        <template #header>
          {{ $tc('relationsList.header') }}
        </template>
        {{ $tc('relationsList.emptyState') }}
      </empty-state>
    </div>
    <div
      v-if="isLoading"
      class="loading-container"
    >
      <loading-indicator v-bind="{ isLoading }" />
    </div>
    <relations-modal
      :open="showModal"
      v-bind="{ language, productName }"
      :relation-types="productRelationTypes"
      @hide="onHide"
      @selected="onSelected"
    />
  </div>
</template>

<script>
import List, { ListType } from '../generic-list/list'
import LoadingIndicator from '@/shared/components/widgets/loading-indicator'
import UcButton from '@/shared/components/forms/uc-button/uc-button'
import setUrlParam from '@/shared/js/set-url-param'
import { createNamespacedHelpers, mapGetters } from 'vuex'
import RelationsModal from './relations-modal'
import UcSnackbar, { ThemeProp as SnackbarTheme, DurationProp as SnackbarDuration } from '@/shared/components/widgets/uc-snackbar'
import EmptyState from '@/catalogs/components/generic-list/empty-state'
import { mergeAllData } from '../../js/merge-definitions-and-data'

const genericListStore = createNamespacedHelpers('genericList')
const detailsStore = createNamespacedHelpers('details')

const apiStatusCodes = {
  'PRODUCT_IS_ADDED': 201,
  'PRODUCT_IS_DUPLICATE': 304,
  'PRODUCT_IS_UNAUTHORIZED': 403,
  'PRODUCT_IS_NOT_FOUND': 404
}

export default {
  name: 'RelationsList',
  components: {
    UcButton,
    LoadingIndicator,
    List,
    RelationsModal,
    UcSnackbar,
    EmptyState
  },
  props: {
    language: {
      type: String,
      required: true
    },
    productGuid: {
      type: String,
      required: true
    },
    id: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      dataUrl: null,
      isLoading: true,
      defaultColumnsConfiguration: {
        'productRelationType.Name': { width: '180px' },
        sku: { width: '180px' },
        variantSku: { width: '180px' },
        name: { width: '180px' }
      },
      showModal: false,
      notifications: [],
      productRelationTypes: []
    }
  },
  computed: {
    ...genericListStore.mapGetters(['lists']),
    ...detailsStore.mapGetters(['currentDetails']),
    ...mapGetters(['definitionsByGuid']),
    hasRows () {
      return this.lists[this.id].rows.length > 0
    },
    productName () {
      return this.currentDetails.name.value
    },
    listType () {
      return ListType.RELATION_LIST
    }
  },
  async created () {
    this.dataUrl = this.getUrl()

    try {
      const response = await this.axios.get('product-relation-types')
      this.productRelationTypes = response.data.map(item => ({ guid: item.Guid, label: item.Name }))
    } catch (error) {
      this.productRelationTypes = []
    }
  },
  methods: {
    ...genericListStore.mapMutations(['removeItemsFromList', 'addItemsToBeginningOfList', 'setCurrentListCount']),
    getUrl (columnKey) {
      const baseUrl = `/products/${this.productGuid}/related-products/${this.language}?sort=${columnKey}`
      return setUrlParam(baseUrl, 'sort', columnKey)
    },
    onSortBy (columnKey) {
      this.dataUrl = this.getUrl(columnKey)
    },
    onInitialLoadFinished (totalItems) {
      this.isLoading = false
      this.$emit('initialLoadFinished')
    },
    onInitialLoadStarted () {
      this.isLoading = true
    },
    onHide () {
      this.showModal = false
    },
    async onClick (selected, clearSelected) {
      const payload = selected.map(row => ({ relatedProductGuid: row.meta.entityGuid, relationType: row.meta.relationTypeGuid, twoWay: false }))
      this.isLoading = true

      try {
        await this.axios.delete(`/products/${this.productGuid}/related-products`, { data: payload })
        this.removeItemsFromList({ listId: this.id, itemGuids: selected.map(x => x.meta.entityGuid) })
        clearSelected()
      } catch (error) {
        const removedProductsCount = payload.length
        const text = this.$tc('relationsList.errorMessages.remove', removedProductsCount, { count: removedProductsCount })
        this.showErrorNotification(text)
      } finally {
        this.isLoading = false
      }
    },
    async addProductsToList (addedProducts) {
      const isAddedProduct = addedProduct => addedProduct.StatusCode === apiStatusCodes.PRODUCT_IS_ADDED
      const addedProductGuids = addedProducts.filter(isAddedProduct).map(addedProduct => addedProduct.RelatedProductGuid)

      if (addedProductGuids.length === 0) {
        return
      }

      try {
        // Get all data to populate list
        const response = await this.axios({
          url: `/products/${this.productGuid}/related-products/${this.language}`,
          params: {
            relatedProductGuids: addedProductGuids
          }
        })

        const rows = mergeAllData(this.definitionsByGuid, response.data, { includeVariants: true })
        this.addItemsToBeginningOfList({ id: this.id, newItems: rows })
        this.setCurrentListCount({ id: this.id, newCount: Number(response.headers['x-total-count']) })
      } catch (error) {
        throw error
      }
    },
    async onSelected (products) {
      this.showModal = false
      this.isLoading = true

      try {
        const payload = {
          relationType: products.relationType,
          twoWay: products.twoWay,
          relations: products.rows.map(row => ({ relatedProductGuid: row.meta.entityGuid }))
        }

        const response = await this.axios.patch(`/products/${this.productGuid}/related-products/`, payload)
        const addedProducts = response.data.Relations

        await this.addProductsToList(addedProducts)
        this.showProductsNotification(addedProducts)
      } catch (error) {
        let text = this.$tc('relationsList.errorMessages.general')

        if (error.response && error.response.status === apiStatusCodes.PRODUCT_IS_UNAUTHORIZED) {
          text = this.$tc('relationsList.errorMessages.unauthorized')
        }

        if (error.response && error.response.status === apiStatusCodes.PRODUCT_IS_NOT_FOUND) {
          text = this.$tc('relationsList.errorMessages.notFound')
        }

        this.showErrorNotification(text)
      } finally {
        this.isLoading = false
      }
    },

    // Methods related to showing notifications. TODO: Move to separate component
    countProducts (products, statusCode) {
      const matchesStatusCode = product => product.StatusCode === statusCode
      return products.filter(matchesStatusCode).length
    },
    concatMessages () {
      const args = [ ...arguments ]
      return args.reduce((accumulator, currentValue) => `${accumulator} ${currentValue}`)
    },
    showProductsNotification (products) {
      const duplicatedProductsCount = this.countProducts(products, apiStatusCodes.PRODUCT_IS_DUPLICATE)
      const notFoundProductsCount = this.countProducts(products, apiStatusCodes.PRODUCT_IS_NOT_FOUND)
      const unauthorizedProductsCount = this.countProducts(products, apiStatusCodes.PRODUCT_IS_UNAUTHORIZED)
      const addedProductsCount = this.countProducts(products, apiStatusCodes.PRODUCT_IS_ADDED)

      const hasErrors = notFoundProductsCount > 0 || unauthorizedProductsCount > 0
      const hasProducts = addedProductsCount > 0 || duplicatedProductsCount > 0

      if (hasErrors) {
        const notFound = this.$tc('relationsList.messages.notFound', notFoundProductsCount, { count: notFoundProductsCount })
        const unauthorized = this.$tc('relationsList.messages.unauthorized', unauthorizedProductsCount, { count: unauthorizedProductsCount })
        const errorMessage = this.concatMessages(notFound, unauthorized)

        this.showErrorNotification(errorMessage)
      }

      if (hasProducts) {
        const success = this.$tc('relationsList.messages.success', addedProductsCount, { count: addedProductsCount })
        const alreadyExist = this.$tc('relationsList.messages.alreadyExist', duplicatedProductsCount, { count: duplicatedProductsCount })
        const notificationMessage = this.concatMessages(success, alreadyExist)

        const onlyDuplicateProducts = addedProductsCount === 0
        const theme = onlyDuplicateProducts ? SnackbarTheme.NEUTRAL : SnackbarTheme.SUCCESS
        this.showNotification({ text: notificationMessage, theme })
      }
    },
    showNotification ({ text, theme, duration = SnackbarDuration.SHORT }) {
      this.notifications.push({
        text,
        dismissable: duration === SnackbarDuration.INFINITE,
        duration,
        theme
      })
    },
    showErrorNotification (text) {
      this.showNotification({ text, theme: SnackbarTheme.ERROR, duration: SnackbarDuration.INFINITE })
    },
    clearFiniteNotifications () {
      this.notifications = this.notifications.filter(notification => notification.theme === SnackbarTheme.ERROR)
    },
    clearNotification (index) {
      this.notifications = this.notifications.filter((notification, i) => i !== index)
    }
  }
}
</script>

<style lang="stylus" scoped>
.loading-container
  display flex
  align-items center
  justify-content center
  height 346px

.relations-list__snackbar ~ .relations-list__snackbar
  margin-top 8px
</style>
