<template>
  <div class="list-wrapper">
    <section class="list-container">
      <header class="list-header-area">
        <section class="header-left">
          <span class="list-heading">{{ header }}</span>
          <span
            v-if="currentList.listCount > 0"
            class="item-count"
          >({{ currentList.listCount }})</span>
        </section>
        <section class="header-right">
          <slot name="actionsHeader" />
          <settings :list-id="id" />
        </section>
      </header>
      <div
        v-if="hasNotificationSlot"
        class="list-container__notifications"
      >
        <slot
          name="notifications"
          :selected="selected"
          :clearSelected="clearSelected"
        />
      </div>
      <list-table
        :ref="listTableRef"
        :list="currentList"
        :total-count="currentList.listCount"
        :is-loading-items="isLoadingItems"
        :selectable-rows="selectableRows"
        :selected="selected"
        @needsMoreData="loadMoreData"
        @columnsDragged="columnsDragged"
        @rowClick="openEntityEditor"
        @sortBy="sortBy"
        @toggleRow="toggleRow"
      >
        <template
          v-if="isLoadingCategory"
          #categoryLoader
        >
          <div class="list-table__loading-indicator">
            <loading-indicator
              :is-loading="isLoadingItems"
            />
          </div>
        </template>
      </list-table>
      <div
        v-if="hasFooterSlot"
        class="list-container__actions-footer"
      >
        <slot
          name="actionsFooter"
          :selected="selected"
          :clearSelected="clearSelected"
        />
      </div>
    </section>
  </div>
</template>

<script>
import MapMetadata from '../../js/map-metadata-response.js'
import GetId from '../../../shared/js/id-generator.js'
import Settings from './settings'
import ListTable from './list-table.vue'
import LoadingIndicator from '../../../shared/components/widgets/loading-indicator'
import { mergeAllData } from '../../js/merge-definitions-and-data'
import { createNamespacedHelpers, mapGetters, mapMutations } from 'vuex'
import setUrlParam from '../../../shared/js/set-url-param'
import { validateAgainstEnum } from '@/shared/js/validate-against'
import { getCancellationToken, isRequestCanceled } from '@/services/http'
import { getEntityService } from '@/services'

const genericListStore = createNamespacedHelpers('genericList')
const detailsStore = createNamespacedHelpers('details')

export const ListType = {
  PRODUCT_LIST: 'productList',
  VARIANT_LIST: 'variantList',
  RELATION_LIST: 'relationList'
}

export default {
  name: 'List',
  components: {
    Settings,
    ListTable,
    LoadingIndicator
  },
  props: {
    id: {
      type: String,
      required: true
    },
    nodeType: {
      type: String,
      required: true
    },
    dataUrl: {
      type: String,
      default: ''
    },
    header: {
      type: String,
      required: true
    },
    selectableRows: {
      type: Boolean,
      default: false
    },
    defaultColumnsConfiguration: {
      type: Object,
      default: () => ({})
    },
    listType: {
      type: String,
      default: ListType.PRODUCT_LIST,
      validate: validateAgainstEnum(ListType)
    }
  },
  data () {
    return {
      isLoadingItems: false,
      entity: undefined,
      isLoadingCategory: false,
      selected: []
    }
  },
  computed: {
    ...mapGetters(['definitionsByGuid']),
    ...genericListStore.mapGetters(['lists']),
    ...detailsStore.mapGetters(['currentDetails']),
    currentList () {
      return this.lists[this.id]
    },
    listTableRef () {
      return `${this.id}Ref`
    },
    hasFooterSlot () {
      return !!this.$scopedSlots.actionsFooter
    },
    hasNotificationSlot () {
      return !!this.$scopedSlots.notifications
    },
    shouldIncludeVariants () {
      return this.listType === ListType.VARIANT_LIST ||
        this.listType === ListType.RELATION_LIST
    }
  },
  watch: {
    dataUrl () {
      this.$refs[this.listTableRef].resetScrollPosition()
      this.load()
    }
  },
  created () {
    this.entityService = getEntityService(this.nodeType)
    this.addListId(this.id)
    this.setListLocalStorageKey({ id: this.id, key: this.getLocalStorageKey('layout1') })
    this.load()
  },
  methods: {
    ...genericListStore.mapMutations([
      'addListId',
      'setListLocalStorageKey',
      'setListColumns',
      'setListRows',
      'addListRows',
      'setCurrentListCount'
    ]),
    ...mapMutations(['setDefinitions']),
    ...detailsStore.mapMutations(['setDetails']),
    ...genericListStore.mapActions(['applyColumnOrder']),
    async load () {
      if (!this.dataUrl) {
        return
      }

      this.isLoadingCategory = true
      this.$emit('initialLoadStarted')

      if (this.cancelToken) {
        this.cancelToken.cancel()
      }
      this.cancelToken = getCancellationToken()

      await this.loadMetadata()
      await this.loadData()

      this.isLoadingCategory = false
      this.clearSelected()
    },
    async loadMetadata () {
      try {
        const definitions = (await this.entityService.fetchDefinitionsAsync({ cancelToken: this.cancelToken.token })).data
        this.setDefinitions(definitions)
        const uniqueColumns = this.groupDefinitionFields(definitions)
        const mappedColumns = this.filterByListType(MapMetadata(uniqueColumns, GetId()))
        this.setListColumns({
          id: this.id,
          columns: this.shouldIncludeVariants
            ? this.includeOnlyCurrentDefinitionFields(mappedColumns)
            : mappedColumns,
          defaultColumnNames: Object.keys(this.defaultColumnsConfiguration),
          initDefaultColumns: true
        })
        this.applyColumnOrder({ id: this.id, currentList: this.currentList })
        return definitions
      } catch (e) {
        if (isRequestCanceled(e)) {
          return
        }
        if (!e.response || e.response.status !== 304) {
          throw e
        }
      }
    },

    filterByListType (columns) {
      let shouldShowVariantPropertiesColumns = this.shouldIncludeVariants
      return columns.filter(x => x.isVariantProperty === shouldShowVariantPropertiesColumns || x.isVariantProperty === null)
    },
    includeOnlyCurrentDefinitionFields (columns) {
      const currentDefinitionGuid = this.currentDetails.meta.definition.guid
      const definitionProperties = this.definitionsByGuid[currentDefinitionGuid]
      let usedPropertiesGuids = []
      Object.keys(definitionProperties).map(function (key, index) {
        usedPropertiesGuids.push(definitionProperties[key].guid)
      })
      const byPropertyGuid = ({ guid }) => usedPropertiesGuids.includes(guid)
      return columns.filter(byPropertyGuid)
    },
    toggleRow (entity) {
      this.toggleSelected(entity)
    },
    toggleSelected (entityGuid) {
      const index = this.selected.indexOf(entityGuid)

      if (index === -1) {
        this.selected.push(entityGuid)
      } else {
        this.selected.splice(index, 1)
      }
    },
    async loadData () {
      this.isLoadingItems = true
      try {
        const result = await this.axios.get(this.dataUrl, {
          cancelToken: this.cancelToken.token
        })
        const mappedResult = this.getFieldsExceptOf(result.data, '')
        this.setCurrentListCount({ id: this.id, newCount: (+result.headers['x-total-count']) || 0 })
        this.setListRows({ id: this.id, rows: mergeAllData(this.definitionsByGuid, mappedResult, { includeVariants: this.shouldIncludeVariants }) })
        this.$emit('initialLoadFinished', this.currentList.listCount)
        this.$nextTick(() => {
          if (!this.$refs[this.listTableRef]) {
            return
          }

          this.$refs[this.listTableRef].initColumnsConfiguration(this.defaultColumnsConfiguration, 'width')
        })
      } catch (e) {
        if (isRequestCanceled(e)) {
          return
        }
        throw e
      } finally {
        this.isLoadingItems = false
      }
    },
    async loadMoreData () {
      if (this.isLoadingItems) return
      if (this.currentList.rows.length >= this.currentList.listCount) return
      this.isLoadingItems = true
      try {
        let urlWithPagination = setUrlParam(this.dataUrl, 'limit', 30)
        urlWithPagination = setUrlParam(urlWithPagination, 'startAt', this.currentList.rows.length)
        const result = await this.axios.get(urlWithPagination, {
          cancelToken: this.cancelToken.token
        })
        const mappedResult = this.getFieldsExceptOf(result.data, '')
        this.addListRows({ id: this.id, rows: mergeAllData(this.definitionsByGuid, mappedResult, { includeVariants: this.shouldIncludeVariants }) })
      } catch (e) {
        if (isRequestCanceled(e)) {
          return
        }
        throw e
      } finally {
        this.isLoadingItems = false
      }
    },
    sortBy (columnKey) {
      this.$emit('sortBy', columnKey)
    },
    columnsDragged (columns) {
      this.setListColumns({ id: this.id, columns: columns })
    },
    groupDefinitionFields (definitions) {
      let map = new Map()
      definitions.forEach(definition => {
        const fields = this.getFieldsExceptOf(definition, '')
        fields.forEach(field => {
          let x = { ...field }
          if (!map.has(x.key)) {
            x.definitions = []
          } else {
            x = map.get(x.key)
          }
          let definitionName = definition.definition.name
          if (definitionName) {
            x.definitions.push(definitionName)
          }
          map.set(x.key, x)
        })
      })
      return [...map.values()]
    },
    getFieldsExceptOf (definition, field) {
      let fieldsKeys = Object.keys(definition).filter(x => x !== field)
      let fieldsObj = fieldsKeys.reduce((obj, key) => ({ ...obj, [key]: definition[key] }), {})
      fieldsKeys.forEach(key => {
        fieldsObj[key].key = key
      })
      return Object.values(fieldsObj)
    },
    getLocalStorageKey (layoutName) {
      return `uc-list-setting-${this.id}-${layoutName}`
    },
    openEntityEditor (row) {
      this.$emit('openEntityEditor', row)
    },
    deleteEntityAndCloseDetails (guid) {
      let newList = this.currentList.rows.filter(x => x.meta.entityGuid !== guid)
      this.setListRows({ id: this.id, rows: newList })
      this.setCurrentListCount({ id: this.id, newCount: newList.length() })
      this.$emit('openEntityEditor', null)
    },
    clearSelected () {
      this.selected = []
    }
  }
}
</script>

<style lang="stylus" scoped>
.list-wrapper
  height 100%

.list-container
  position relative
  display flex
  flex-direction column
  height 100%
  margin 0 auto
  padding 0
  background $ucommerce-white
  user-select none

.list-header-area
  display flex
  align-items center
  justify-content space-between
  margin 24px 20px

  & > section
    display flex
    flex-direction row

    & > .uc-button
      margin-right 16px

  .list-heading
    primary-heading-2()

  .item-count
    primary-heading-2()
    margin-left 10px
    font-weight normal

  .list-header-area
    position relative
    z-index 105
    margin-top 30px
    cursor pointer

    & > .back-icon
      box(12px)
      transform rotate(180deg)

    & > .back-label
      primary-details-text()
      margin-left 6px

.list-table__loading-indicator
  position absolute
  z-index 105
  display flex
  align-items center
  justify-content center
  width 100%
  height 100%
  background $ucommerce-white

.list-container__actions-footer
  position absolute
  right 0
  bottom 0
  left 0
  z-index 104 // Necessary to overflow notification and list header
  display flex
  align-items center
  justify-content center
  padding 60px 40px 20px 40px
  overflow hidden
  pointer-events none

.list-container__notifications
  position absolute
  top 130px
  right 0
  left 0
  z-index 102
  padding 16px

  &:empty
    display none

</style>
