<template>
  <div
    class="uc-navigator-grid-branch-node"
    data-test="grid-branch-node"
    @click="select"
  >
    <div
      class="uc-navigator-grid-branch-node__container"
      :class="cssClassObject"
    >
      <slot :node="node" />
      <div class="uc-navigator-grid-branch-node__meta">
        {{ childCountLabel }}
      </div>
    </div>
    <div class="uc-navigator-grid-branch-node__button">
      <uc-button-round
        theme="ghost"
        size="s"
        @click.stop="open"
      >
        <chevron-forward />
      </uc-button-round>
    </div>
    <div
      v-truncate
      class="uc-navigator-grid-branch-node__name"
    >
      {{ node.name }}
    </div>
  </div>
</template>

<script>
import UcButtonRound from '@/shared/components/forms/uc-button-round'
import ChevronForward from '@/shared/icons/16/chevron-forward--16--thick'

const ComponentEvent = {
  OPEN: 'open',
  SELECT: 'select'
}

export default {
  name: 'UcNavigatorGridBranchNode',
  components: {
    UcButtonRound,
    ChevronForward
  },
  props: {
    selected: {
      type: Boolean,
      default: false
    },
    node: {
      type: Object,
      required: true
    }
  },

  computed: {
    cssClassObject () {
      return {
        'is-selected': this.selected
      }
    },
    childCountLabel () {
      return this.$tc('contentNavigator.items', this.node.childrenCount)
    }
  },
  methods: {
    select () {
      this.$emit(ComponentEvent.SELECT, this.node)
    },
    open () {
      this.$emit(ComponentEvent.OPEN, this.node)
    }
  }
}
</script>
<style lang="stylus" scoped>
.uc-navigator-grid-branch-node
  position relative
  border 1px solid transparent

.uc-navigator-grid-branch-node__container
  display flex
  flex-direction column
  align-items center
  justify-content center
  width 100%
  height 150px
  border solid 1px $col_border
  border-radius ($base-unit / 2)
  cursor pointer

.uc-navigator-grid-branch-node__name
  secondary-petit-text()
  margin 0
  padding ($base-unit / 2) 0 0 0

.uc-navigator-grid-branch-node__meta
  secondary-petit-text()
  margin 0
  padding ($base-unit / 2) 0 0 0

.uc-navigator-grid-branch-node__button
  position absolute
  right 1.5 * $base-unit
  bottom 4 * $base-unit
  box(3 * $base-unit)

// :hover
.uc-navigator-grid-branch-node__container:hover
  border-color $ucommerce-blue

// .is-selected
.uc-navigator-grid-branch-node__container.is-selected
  border 2px solid $ucommerce-blue
  border-color $ucommerce-blue

// :active
.uc-navigator-grid-branch-node:active
  .uc-navigator-grid-branch-node__container
    transform scale(0.985)
</style>
