<template>
  <div
    class="uc-boolean"
  >
    <editor-wrapper
      v-bind="{ compact, errors }"
      :compact-value="translatedValue"
    >
      <toggle
        v-bind="{ label, guid, compact }"
        :readonly="isReadonly"
        :checked="value"
        :tooltip="description"
        @change="onChange({ newValue: $event.value, oldValue: value })"
      />
    </editor-wrapper>
  </div>
</template>

<script>
import Toggle from '../forms/uc-toggle'
import editorMixin from './editor-mixin'

export default {
  name: 'UcBoolean',
  components: {
    Toggle
  },
  mixins: [ editorMixin ],
  computed: {
    translatedValue () {
      const answer = this.value ? 'yes' : 'no'
      return this.$t(`global.${answer}`)
    }
  }
}
</script>
