<template>
  <div class="uc-wizard">
    <uc-stepper
      :slot-prefix="slotPrefix"
      @change="change"
    >
      <!-- Back button -->
      <template v-slot:top-left="{next, previous, step}">
        <transition name="fade">
          <div
            v-show="step > 1"
            class="uc-wizard__previous"
          >
            <uc-button
              theme="link"
              size="sm"
              data-test="previous"
              @click="previous"
            >
              {{ $tc('global.back') }}
              <template #before>
                <arrow-back />
              </template>
            </uc-button>
          </div>
        </transition>
      </template>

      <!-- Panels -->
      <template
        v-for="stepSlotName in stepSlotNames"
        v-slot:[stepSlotName]="{ next, previous, step, stepCount }"
        :name="stepSlotName"
      >
        <slot
          :name="stepSlotName"
          v-bind="{ next, previous, step, stepCount }"
        />
      </template>

      <!-- Next button -->
      <template v-slot:after="{ next, previous, step, stepCount }">
        <transition name="fade">
          <div
            v-show="step < stepCount"
            class="uc-wizard__next"
          >
            <uc-button
              :disabled="disableNext"
              data-test="next"
              @click="next"
            >
              {{ $tc('global.continue') }}
            </uc-button>
          </div>
        </transition>
      </template>
    </uc-stepper>
  </div>
</template>

<script>
import UcButton from '@/shared/components/forms/uc-button'
import UcStepper from '@/shared/components/generic/uc-stepper/uc-stepper'
import { ArrowBack16Normal as ArrowBack } from '@/shared/icons'
export default {
  components: {
    UcStepper,
    UcButton,
    ArrowBack
  },
  props: {
    disableNext: {
      type: Boolean,
      default: false
    },
    slotPrefix: {
      type: String,
      default: 'step-'
    }
  },
  computed: {
    stepSlotNames () {
      return Object.keys(this.$scopedSlots).filter(slotName =>
        slotName.startsWith(this.slotPrefix)
      )
    }
  },
  methods: {
    change (step) {
      this.$emit('change', step)
    }
  }
}
</script>

<style lang="stylus" scoped>
.uc-wizard
  height 100%

.uc-wizard__next
  display flex
  flex-direction column
  align-items center
  justify-content center
</style>
