<template>
  <div class="products-navigate">
    <!-- Navigation -->
    <catalog-builder />

    <!-- Product listing  -->
    <div class="products-navigate__listing">
      <fields-section paddingless>
        <list
          :id="listId"
          v-bind="{ nodeType, dataUrl, defaultColumnsConfiguration }"
          :header="$tc('list.products')"
          @sortBy="onSortBy"
          @openEntityEditor="openEntityEditor"
        >
          <template #actionsHeader>
            <transition name="fade">
              <span
                v-if="isProductCategory"
                class="products-navigate__button"
              >
                <uc-button
                  size="sm"
                  @click="toggleDialog"
                >
                  {{ $t('list.newButton') }}
                </uc-button>
              </span>
            </transition>
          </template>
        </list>
      </fields-section>
    </div>
  </div>
</template>

<script>
import CatalogBuilder from '@/catalogs/components/catalog-builder/catalog-builder.vue'
import List from '@/catalogs/components/generic-list/list'
import UcButton from '@/shared/components/forms/uc-button'
import FieldsSection from '@/shared/components/sections/fields-section'

import setUrlParam from '@/shared/js/set-url-param'
import { nodeTypeApiMap } from '@/catalogs/js/tree-node-types'
import { EntityType } from '@/shared/js/constants'

export default {
  name: 'ProductsNavigate',
  components: {
    FieldsSection,
    CatalogBuilder,
    List,
    UcButton
  },
  props: {
    listId: {
      type: String,
      required: true
    },
    contentLanguage: {
      type: String,
      required: true
    },
    displayedNode: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      sortBy: ''
    }
  },
  computed: {
    isProductCategory () {
      return this.displayedNode.nodeType === EntityType.CATEGORY
    },
    nodeType () {
      return EntityType.PRODUCT
    },
    defaultColumnsConfiguration () {
      return {
        'primaryImage': {
          'width': '90px'
        },
        'sku': {},
        'name': {
          'width': '250px'
        },
        'definition': {},
        'eur 15 pct': {
          'width': '360px'
        }
      }
    },
    dataUrl () {
      let url = this.displayedNode.nodeType ? `/${nodeTypeApiMap[this.displayedNode.nodeType]}/${this.displayedNode.guid}/products/${this.contentLanguage}` : ''
      url = setUrlParam(url, 'sort', this.sortBy)

      return url
    }
  },
  methods: {
    onSortBy (sortBy) {
      this.sortBy = sortBy
    },
    toggleDialog () {
      this.$emit('toggle-dialog')
    },
    openEntityEditor (row) {
      this.$emit('open-entity-editor', row)
    },
    closeEntityEditor () {
      this.$emit('close-entity-editor')
    }
  }
}
</script>

<style lang="stylus" scoped>
.products-navigate__listing
  margin-top 4 * $base-unit

.products-navigate__button
  margin-right 2 * $base-unit
</style>
