<template>
  <span
    class="uc-search"
    data-test="uc-search"
    :class="{ 'uc-search--dark': dark, 'uc-search--dense': dense }"
  >
    <i
      class="uc-search__icon icon icon--search"
      @click="focusInput"
    >
      <component :is="searchIcon" />
    </i>
    <input
      ref="searchInput"
      type="text"
      class="uc-search__input input"
      spellcheck="false"
      :value="inputValue"
      :placeholder="placeholder"
      @input="onInput($event.target.value)"
    >
    <i
      v-show="inputValue"
      class="uc-search__icon icon icon--clear"
      data-test-id="clear-search"
      @click="onClear"
    >
      <component :is="closeIcon" />
    </i>
  </span>
</template>

<script>
import {
  Search16Normal as SearchIcon,
  Close16Normal as CloseIcon,
  Search24Normal as SearchIconLarge,
  Close24Normal as CloseIconLarge
} from '../../../icons'
import debounce from '../../../js/debounce'

export default {
  name: 'UcSearch',
  components: {
    SearchIcon,
    CloseIcon,
    SearchIconLarge,
    CloseIconLarge
  },
  props: {
    value: {
      type: String,
      default: ''
    },
    focus: {
      type: Boolean,
      default: false
    },
    clear: {
      type: Boolean,
      default: false
    },
    dark: {
      type: Boolean,
      default: false
    },
    dense: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: function () {
        return this.$tc('global.search')
      }
    }
  },
  data () {
    return {
      inputValue: ''
    }
  },
  computed: {
    searchIcon () {
      return this.dense ? SearchIcon : SearchIconLarge
    },
    closeIcon () {
      return this.dense ? CloseIcon : CloseIconLarge
    }
  },
  watch: {
    value: {
      handler (newValue) {
        this.inputValue = newValue
      },
      immediate: true
    },
    focus (newValue) {
      if (newValue) {
        this.focusInput()
      }
    },
    clear (newValue) {
      if (newValue) {
        this.onClear()
      }
    }
  },
  methods: {
    setValue (value) {
      this.inputValue = value
      this.$emit('input', this.inputValue)
    },
    focusInput () {
      this.$refs.searchInput.focus()
    },
    onInput: debounce(function (value) {
      this.setValue(value)
    }, 250),
    onClear () {
      this.setValue('')
      this.focusInput()
    }
  }
}
</script>

<style lang="stylus" scoped>
$space = 8px

.uc-search
  display flex
  align-items center
  box-sizing border-box
  height 6 * $space
  border 1px solid $col_light_border
  border-radius 3 * $space
  background-color $ucommerce-white

  &.uc-search--dark
    background-color $platform-bg

  &.uc-search--dense
    height 4 * $space

input.uc-search__input
  box(100%)
  flex-shrink 1
  padding 12px 0
  border none
  background-color transparent
  outline none
  primary-body-text()

  .uc-search--dense &
    padding 6px 0
    primary-details-text()

  &::placeholder
    secondary-body-text()

    .uc-search--dense &
      secondary-details-text()

.icon
  display flex
  flex-shrink 0
  align-items center
  justify-content center

  &.icon--search
    padding (1.5 * $space) $space (1.5 * $space) (1.5 * $space)

  &.icon--clear
    padding (1.5 * $space) (1.5 * $space) (1.5 * $space) $space
    cursor pointer

  .uc-search--dense &
    padding $space
</style>
